import { useEffect, useState } from "react";
import VideoBackground from "./VideoBackground";
import GamePanel from "./GamePanel";
import { useWindowSize } from "@uidotdev/usehooks";

const TabletGame: React.FC<any> = ({ lastMessage, sendMessage }) => {
  const size = useWindowSize();
  const [windowSize, setWindowSize] = useState({ width: 0, height: 0 });

  const [status, setStatus] = useState(false);
  useEffect(() => {
    setStatus(true);
  }, []);
  useEffect(() => {
    if (size.width && size.height) {
      const d = size.width / size.height;
      if (d > 16 / 9) {
        setWindowSize({
          width: Number(((size.height / 9) * 16).toFixed(0)),
          height: size.height,
        });
      } else {
        setWindowSize({
          width: size.width,
          height: Number(((size.width * 9) / 16).toFixed(0)),
        });
      }
    }
  }, [size]);

  return (
    <div className="game-container">
      <div
        style={
          !status
            ? {
                width: 1920,
                height: 1080,
                display: "none",
              }
            : {
                transform: `scale(${(windowSize.width / 1920) * 0.85}, ${
                  (windowSize.height / 1080) * 0.85
                })`,
                transition: "all",
                transitionDelay: "150",
                width: 1920,
                height: 1080,
              }
        }
        className="absolute overflow-hidden roulette-page"
      >
        <VideoBackground />
        <GamePanel lastMessage={lastMessage} sendMessage={sendMessage} />
      </div>
    </div>
  );
};

export default TabletGame;
