  const getNumbers = (num: number) => {
    const indexNumber = numberList.indexOf(num);
    const selectedNumberList = [];
    for (
      let i = indexNumber - raceTrackCount;
      i <= indexNumber + raceTrackCount;
      i++
    ) {
      selectedNumberList.push(numberList[(i + 37) % 37]);
    }
    return selectedNumberList;
  };
  const handlePathEnter = (value: Array<number>) => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      setRaceTrackActiveItems(value);
    }
  };
  const handlePathLeave = () => {
    setRaceTrackActiveItems([]);
  };
  const handlePathSelect = (valueList: Array<number>) => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      const estimateAmount =
        valueList.length * tokenContext.tokenStatus.currentToken;

      if (Number(balanceContext.balanceStatus.totalBalance) >= estimateAmount) {
        const newSelectedToken: Array<any> =
          tokenContext.tokenStatus.selectedToken;
        const selectedToken: Array<any> = [];
        valueList.forEach((value) => {
          selectedToken.push({
            value: [value],
            amount: tokenContext.tokenStatus.currentToken,
            type: "racetrack",
          });
        });
        newSelectedToken.push(selectedToken);
        tokenContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceContext.setBalanceStatus({
          totalBalance:
            balanceContext.balanceStatus.totalBalance - estimateAmount,
          betBalance: balanceContext.balanceStatus.betBalance + estimateAmount,
        });
      }
    }
  };
