import React, { useState } from "react";

import LogoImage from "../../../assets/img/logopng1.png";
import { ReactComponent as CircleMinus } from "../../../assets/img/circle-minus.svg";
import { ReactComponent as CirclePlus } from "../../../assets/img/circle-plus.svg";

import RaceTrackMask from "./Mask";
import RaceTrackMainBoard from "./MainBoard";
import RaceTrackTopSelect from "./TopSelect";

const RaceTrackBoard: React.FC = () => {
  const [activeTokenNumbers, setActiveTokenNumbers] = useState<Array<any>>([]);
  const [raceTrackCount, setRaceTrackCount] = useState<number>(2);

  const handleCountIncrease = () => {
    setRaceTrackCount((prev) => {
      if (prev + 1 > 9) return prev;
      return prev + 1;
    });
  };

  const handleCountDecrease = () => {
    setRaceTrackCount((prev) => {
      if (prev - 1 < 1) return prev;
      return prev - 1;
    });
  };

  return (
    <div className="col-span-4 relative pe-8">
      <div className="px-2 h-full w-full flex items-end">
        <div className="flex flex-col w-full">  
          <div className="ps-[30px] pe-[60px] w-full ">
            <RaceTrackTopSelect />
          </div>
          <div className="flex w-full gap-2">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              xmlnsXlink="http://www.w3.org/1999/xlink"
              className="w-full"
              viewBox="0 0 440.716 157.499"
            >
              <g id="RaceTrack" transform="translate(-1392.141 -846)">
                <RaceTrackMainBoard activeTokenNumbers={activeTokenNumbers} />
                <RaceTrackMask
                  raceTrackCount={raceTrackCount}
                  setActiveTokenNumbers={setActiveTokenNumbers}
                />
              </g>
            </svg>
            <div className="flex flex-col h-[150px] justify-evenly">
              <button
                onClick={handleCountIncrease}
                className="w-6 h-6 rounded-full text-center hover:text-[#E9C84D] text-[#7E7243] text-[30px] flex items-center justify-center font-bold"
              >
                <CirclePlus className="w-full fill-current" />
              </button>
              <button className="text-[#E9C84D] text-[24px] font-bold">
                {raceTrackCount}
              </button>
              <button
                onClick={handleCountDecrease}
                className="w-6 h-6 rounded-full text-center hover:text-[#E9C84D] text-[#7E7243] text-[30px] flex items-center justify-center font-bold"
              >
                <CircleMinus className="w-full fill-current" />
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="absolute w-full h-[190px] bottom-[210px]">
        <div className="w-full h-full bg-black/70 blur-lg rounded-[50%]"></div>
      </div>
      <div className="absolute bottom-[220px] w-full h-fit">
        <img className="w-full h-auto" src={LogoImage} alt="logo" />
      </div>
    </div>
  );
};

export default RaceTrackBoard;
