import React, { useContext } from "react";
import clsx from "classnames";

import socketEventsCode from "../../../../../assets/data/SocketEventCode";
import GameStatusContext from "../../../../../context/GameStatusContext";
interface INumAreaProps {
  areaTransform: string;
  titleTransform: string;
  tspan: string;
  title: number;
  fillColor: string;
  key: number;
  activeButtonList: Array<number>;
}

const NumArea: React.FC<INumAreaProps> = ({
  areaTransform,
  titleTransform,
  tspan,
  title,
  fillColor,
  activeButtonList,
}) => {
  const gameStatusContext = useContext(GameStatusContext);

  return (
    <g>
      <g transform={areaTransform}>
        <g
          className={clsx(
            "transition-all duration-300"
          )}
          fill={`${
            activeButtonList.indexOf(title) !== -1 ? "#B18F12" : fillColor
          }`}
          stroke="#b9a978"
          strokeWidth="1"
          fillOpacity={
            gameStatusContext.gameStatus ===
              socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
            gameStatusContext.gameStatus ===
              socketEventsCode.SOCKET_GAME_FINISHED
              ? activeButtonList.indexOf(title) === -1 &&
                activeButtonList.length > 0
                ? 0.5
                : 0.9
              : 0.5
          }
        >
          <rect width="58" height="58" rx="3" stroke="none" />
          <rect x="0.5" y="0.5" width="57" height="57" rx="2.5" fill="none" />
        </g>
      </g>
      <text
        transform={titleTransform }
        fill="#e7e7e7"
        fontSize="30"
        fontWeight={600}
        fontFamily="Montserrat"
      >
        <tspan x={tspan} y="0" className="">
          {title}
        </tspan>
      </text>
    </g>
  );
};

export default NumArea;
