import React, { useState } from "react";
import { PortraitMenu } from "../../../components/Game/GameMenu";
import PortraitMainPanel from "./MainPanel";

const PortraitPanel: React.FC = () => {
  const [isMainBoard, setIsMainBoard] = useState<boolean>(false);
  return (
    <div className="w-full h-[calc(100%-20px)] pt-4 pb-10  bg-gradient-to-b from-transparent from-25% via-[#110C00]/80 via-80% to-[#050400]">
      <div className="flex justify-between h-full w-full">
        <div className="w-full h-full flex justify-center items-end">
          <PortraitMainPanel isMainBoard={isMainBoard} />
        </div>
        <PortraitMenu setIsMainBoard={setIsMainBoard} />
      </div>
    </div>
  );
};

export default PortraitPanel;
