import React from "react";

import { ReactComponent as BetTypeIcon } from "../../../assets/img/bet-type.svg";
import { ReactComponent as BetLimitIcon } from "../../../assets/img/bet-limit.svg";
import { ReactComponent as PayoutIcon } from "../../../assets/img/payout.svg";
interface IBetLimitModalProps {
  isShowModal: boolean;
  isModalPined: boolean;
  setIsShowModal: (value: boolean) => void;
}
const BetLimitModal: React.FC<IBetLimitModalProps> = ({
  isShowModal,
  isModalPined,
  setIsShowModal,
}) => {
  return (
    <div
      onMouseEnter={() => setIsShowModal(true)}
      onMouseLeave={() => setIsShowModal(false)}
      className={`fixed left-[-500px] top-[0px] z-10 w-[500px] settings-container bg-gradient-to-b from-[#000000]/90 to-[#392D08]/90 duration-300 ${
        isShowModal || isModalPined
          ? "translate-x-[500px] "
          : "translate-x-[0px]"
      }`}
    >
      <div className="mt-[60px] text-white">
        <div className="p-4">
          <div className="grid grid-cols-3 border-b border-[white] text-[#DDB652] pb-2">
            <h1 className="flex gap-1">
              <BetTypeIcon className="h-6" />
              BET
            </h1>
            <h1 className="justify-end flex gap-1">
              <BetLimitIcon className="h-6" />
              BET LIMITES
            </h1>
            <h1 className="justify-end flex gap-1">
              <PayoutIcon className="h-6" />
              PAYOUT
            </h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73] mt-[20px]">
            <h1>STRAIGHT UP</h1>
            <h1 className="text-left ps-4">€ 0.25-50,000</h1>
            <h1 className="text-right">29-499:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>SPLIT</h1>
            <h1 className="text-left ps-4">€ 0.25-100,000</h1>
            <h1 className="text-right">17:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>STREET</h1>
            <h1 className="text-left ps-4">€ 0.25-150,000</h1>
            <h1 className="text-right">11:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>CORNER</h1>
            <h1 className="text-left ps-4">€ 0.25-200,000</h1>
            <h1 className="text-right">8:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>LINE</h1>
            <h1 className="text-left ps-4">€ 0.25-300,000</h1>
            <h1 className="text-right">5:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>COLUMN</h1>
            <h1 className="text-left ps-4">€ 0.25-600,000</h1>
            <h1 className="text-right">2:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>DOZEN</h1>
            <h1 className="text-left ps-4">€ 0.25-600,000</h1>
            <h1 className="text-right">2:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>RED/BLACK</h1>
            <h1 className="text-left ps-4">€ 0.25-1,000,000</h1>
            <h1 className="text-right">1:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>EVEN/ODD</h1>
            <h1 className="text-left ps-4">€ 0.25-1,000,000</h1>
            <h1 className="text-right">1:1</h1>
          </div>
          <div className="grid grid-cols-3 text-[#C3AE73]">
            <h1>1-18/19-36</h1>
            <h1 className="text-left ps-4">€ 0.25-1,000,000</h1>
            <h1 className="text-right">1:1</h1>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BetLimitModal;
