import React, { useState } from "react";
import ReactSlider from "react-slider";

import { ReactComponent as SoundIcon } from "../../../assets/img/soundIcon.svg";
import { ReactComponent as CircleMinus } from "../../../assets/img/circle-minus.svg";
import { ReactComponent as CirclePlus } from "../../../assets/img/circle-plus.svg";

const Sound: React.FC = () => {
  const [videoSound, setVideoSound] = useState<number>(50);
  const [gameEffect, setGameEffect] = useState<number>(50);
  const [allSound, setAllSound] = useState<number>(50);

  const handleVideoSoundChange = (value: number) => {
    setVideoSound((prev: number) => {
      if (prev + value < 0 || prev + value > 100) return prev;
      else return prev + value;
    });
  };

  const handleGameEffectChange = (value: number) => {
    setGameEffect((prev: number) => {
      if (prev + value < 0 || prev + value > 100) return prev;
      else return prev + value;
    });
  };

  const handleAllSoundChange = (value: number) => {
    setAllSound((prev: number) => {
      if (prev + value < 0 || prev + value > 100) return prev;
      else return prev + value;
    });
  };

  return (
    <div className="flex flex-col py-4 text-sm">
      <h1 className="flex gap-2 items-center text-[#ddb652] text-sm font-bold">
        <SoundIcon className="fill-current w-4" />
        AUDIO
      </h1>
      <div className="flex flex-col gap-4 py-2 px-6">
        <div className="flex justify-between">
          <div className="text-[#9E7512] ">
            <h1 className="text-md font-bold ">Video Sounds</h1>
            <p className="text-[10px]">Adjust studio volume</p>
          </div>
          <div className="flex items-center gap-2">
            <div className="max-w-xl mx-auto flex items-center gap-1 ">
              <button
                onClick={() => handleVideoSoundChange(-1)}
                className="text-[#5a4617] hover:text-[#DDAE51]"
              >
                <CircleMinus className="w-4 fill-current" />
              </button>

              <ReactSlider
                className="horizontal-slider w-[100px] h-4 cursor-pointer rounded-full flex items-center relative"
                min={0}
                max={100}
                value={videoSound}
                onChange={(value) => setVideoSound(Number(value))}
                step={1}
                thumbActiveClassName="bg-[#DDAE51] h-3"
                thumbClassName="w-4 h-3 bg-[#DDAE51] rounded-full"
                trackClassName-0="track-0"
                trackClassName-1="track-1"
              />
              <button
                onClick={() => handleVideoSoundChange(1)}
                className="text-[#5a4617] hover:text-[#DDAE51] "
              >
                <CirclePlus className="ms-1 w-4 fill-current" />
              </button>
            </div>
          </div>
        </div>
        <div className="flex justify-between">
          <div className="text-[#9E7512]">
            <h1 className="text-md font-bold">Game Effects</h1>
            <p className="text-[10px]">Adjust game effects level</p>
          </div>
          <div className="flex items-center gap-2">
            <div className="max-w-xl mx-auto flex items-center gap-1 ">
              <button
                onClick={() => handleGameEffectChange(-1)}
                className="text-[#5a4617] hover:text-[#DDAE51] "
              >
                <CircleMinus className="w-4 fill-current" />
              </button>
              <ReactSlider
                className="horizontal-slider w-[100px] h-4 cursor-pointer rounded-full flex items-center relative"
                min={0}
                max={100}
                value={gameEffect}
                step={1}
                onChange={(value) => setGameEffect(Number(value))}
                thumbActiveClassName="bg-[#DDAE51] h-3"
                thumbClassName="w-4 h-3 bg-[#DDAE51] rounded-full"
                trackClassName-0="track-0"
                trackClassName-1="track-1"
              />
              <button
                onClick={() => handleGameEffectChange(1)}
                className="text-[#5a4617] hover:text-[#DDAE51] "
              >
                <CirclePlus className="ms-1 w-4 fill-current" />
              </button>
            </div>
          </div>
        </div>
        <div className="flex justify-between">
          <div className="text-[#9E7512]">
            <h1 className="text-md font-bold">All Sounds</h1>
            <p className="text-[10px]">Adjust all sounds level</p>
          </div>
          <div className="flex items-center gap-2">
            <div className="max-w-xl mx-auto flex items-center gap-1 ">
              <button
                onClick={() => handleAllSoundChange(-1)}
                className="text-[#5a4617] hover:text-[#DDAE51] "
              >
                <CircleMinus className="w-4 fill-current" />
              </button>
              <ReactSlider
                className="horizontal-slider w-[100px] h-4 cursor-pointer rounded-full flex items-center relative"
                min={0}
                max={100}
                value={allSound}
                onChange={(value) => setAllSound(Number(value))}
                step={1}
                thumbActiveClassName="bg-[#DDAE51] h-3"
                thumbClassName="w-4 h-3 bg-[#DDAE51] rounded-full"
                trackClassName-0="track-0"
                trackClassName-1="track-1"
              />
              <button
                onClick={() => handleAllSoundChange(1)}
                className="text-[#5a4617] hover:text-[#DDAE51] "
              >
                <CirclePlus className="ms-1 w-4 fill-current" />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Sound;
