import React, { useState, useContext, useEffect } from "react";
import clsx from "classnames";
import TokenStatusContext from "../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../context/BalanceStatusContext";
import GameStatusContext from "../../../../context/GameStatusContext";

import { ReactComponent as Coin1 } from "../../../../assets/img/coin/coin1.svg";
import { ReactComponent as Coin2 } from "../../../../assets/img/coin/coin2.svg";
import { ReactComponent as Coin3 } from "../../../../assets/img/coin/coin3.svg";
import { ReactComponent as Coin4 } from "../../../../assets/img/coin/coin4.svg";
import { ReactComponent as Coin5 } from "../../../../assets/img/coin/coin5.svg";
import { ReactComponent as Coin6 } from "../../../../assets/img/coin/coin6.svg";
import { ReactComponent as Coin7 } from "../../../../assets/img/coin/coin7.svg";

import socketEventsCode from "../../../../assets/data/SocketEventCode";

const coins = [
  {
    amount: 0.25,
    image: <Coin1 className="w-full" />,
  },
  {
    amount: 0.5,
    image: <Coin2 className="w-full" />,
  },
  {
    amount: 1,
    image: <Coin3 className="w-full" />,
  },
  {
    amount: 5,
    image: <Coin4 className="w-full" />,
  },
  {
    amount: 25,
    image: <Coin5 className="w-full" />,
  },
  {
    amount: 100,
    image: <Coin6 className="w-full" />,
  },
  {
    amount: 500,
    image: <Coin7 className="w-full" />,
  },
];
const PortraitCoinSelect: React.FC = () => {
  const [isCoinSelect, setIsCoinSelect] = useState<boolean>(false);
  const tokenContext = useContext(TokenStatusContext);
  const balanceContext = useContext(BalanceStatusContext);
  const gameContext = useContext(GameStatusContext);

  const [selectCoins, setSelectCoins] = useState<any[]>([]);
  const [selectedCoin, setSelectedCoin] = useState<any>({});

  const handleCoinSelect = (value: number) => {
    setIsCoinSelect((prev) => !prev);
    tokenContext.setTokenStatus({
      ...tokenContext.tokenStatus,
      currentToken: value,
    });
  };

  useEffect(() => {
    const newSelectableToken = coins.filter(
      (coin: any) => coin.amount !== tokenContext.tokenStatus.currentToken
    );
    const newSlectedToken = coins.filter(
      (coin: any) => coin.amount === tokenContext.tokenStatus.currentToken
    );
    setSelectCoins(newSelectableToken);
    setSelectedCoin(newSlectedToken[0]);
  }, [tokenContext.tokenStatus.currentToken]);
  return (
    <div>
      <div className="relative flex justify-center items-center ">
        <div
          className={clsx(
            "absolute top-6 left-12 bg-[#B39752]/30 rounded-full",
            "transition-all duration-300",
            isCoinSelect ? "w-80 h-80" : "w-10 h-10",
            isCoinSelect ? "-translate-x-1/2 -translate-y-1/2" : "translate-x-1/2",
          )}
        ></div>
        <div
          className={clsx(
            "absolute top-6 left-12 bg-[#000000]/80 rounded-full",
            "border-2 border-[#FFEEB2]/60",
            "transition-all duration-300",
            isCoinSelect ? "w-60 h-60" : "w-10 h-10",
            isCoinSelect ? "-translate-x-1/2 -translate-y-1/2" : "translate-x-1/2",
          )}
        ></div>
        <div className="w-10 h-10">
          {selectCoins.map((coin, index) => (
            <button
              key={index}
              onClick={() => handleCoinSelect(coin.amount)}
              className={clsx(
                "absolute bottom-[-6px] left-0 w-8",
                isCoinSelect && `coin-select-${index + 1}`
              )}
            >
              {coin.image}
            </button>
          ))}
          {isCoinSelect && (
            <div className="absolute top-[-5px] left-[-7px] w-[54px] h-[54px] rounded-full border-4 border-[#F2D078]"></div>
          )}
          <button
            onClick={() => handleCoinSelect(selectedCoin.amount)}
            className={`absolute bottom-[-6px] right-0 w-10`}
          >
            {selectedCoin.image}
          </button>
        </div>
      </div>
    </div>
  );
};

export default PortraitCoinSelect;
