import React, { useState, useEffect, useContext } from "react";
import GameStatusContext from "../../../../../../context/GameStatusContext";
import TokenStatusContext from "../../../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../../../../context/GlobalActiveItemsContext";

import socketEventsCode from "../../../../../../assets/data/SocketEventCode";
import * as CellData from "../../../../../../assets/data/CellData";

interface IRaceTrackHistoryProps {
  selectedHistory: Array<any>;
}
const RaceTrackHistory: React.FC<IRaceTrackHistoryProps> = ({
  selectedHistory,
}) => {
  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);
  const [raceTrackHistory, setRaceTrackHistory] = useState({
    tier: 0,
    orphelins: 0,
    voisins: 0,
    zero: 0,
  });
  const calcRaceTrackHistory = () => {
    const newRaceTrackHistory = {
      tier: 0,
      orphelins: 0,
      voisins: 0,
      zero: 0,
    };
    selectedHistory.forEach((history) => {
      const { winNumber } = history;
      if (
        CellData.RaceTrackCell[0].tokenButtons[0].value.indexOf(winNumber) !==
        -1
      )
        newRaceTrackHistory.tier++;
      else if (
        CellData.RaceTrackCell[1].tokenButtons[0].value.indexOf(winNumber) !==
        -1
      )
        newRaceTrackHistory.orphelins++;
      else {
        if (
          CellData.RaceTrackCell[3].tokenButtons[0].value.indexOf(winNumber) !==
          -1
        )
          newRaceTrackHistory.zero++;
        // if (
        //   CellData.RaceTrackCell[2].tokenButtons[0].value.indexOf(winNumber) !==
        //   -1
        // )
        else newRaceTrackHistory.voisins++;
      }
    });
    const total =
      newRaceTrackHistory.tier +
      newRaceTrackHistory.orphelins +
      newRaceTrackHistory.voisins +
      newRaceTrackHistory.zero;
    setRaceTrackHistory({
      tier: Number(((100 * newRaceTrackHistory.tier) / total).toFixed(0)),
      orphelins: Number(
        ((100 * newRaceTrackHistory.orphelins) / total).toFixed(0)
      ),
      voisins: Number(((100 * newRaceTrackHistory.voisins) / total).toFixed(0)),
      zero: Number(((100 * newRaceTrackHistory.zero) / total).toFixed(0)),
    });
  };
  useEffect(() => {
    calcRaceTrackHistory();
  }, [selectedHistory]);
  const handleMouseEnterEvent = (value: Array<number>) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
      globalActiveItemsContext.setGlobalActiveItems(value);
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (valueList: Array<number>) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      const estimateAmount =
        valueList.length * tokenStatusContext.tokenStatus.currentToken;

      if (
        Number(balanceStatusContext.balanceStatus.totalBalance) >=
        estimateAmount
      ) {
        const newSelectedToken: Array<any> =
          tokenStatusContext.tokenStatus.selectedToken;
        const selectedToken: Array<any> = [];
        valueList.forEach((value) => {
          selectedToken.push({
            value: [value],
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          });
        });
        newSelectedToken.push(selectedToken);
        tokenStatusContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceStatusContext.setBalanceStatus({
          totalBalance:
            balanceStatusContext.balanceStatus.totalBalance - estimateAmount,
          betBalance:
            balanceStatusContext.balanceStatus.betBalance + estimateAmount,
        });
      }
    }
  };

  return (
    <div className="flex px-4 w-full h-[40px] text-sm gap-1">
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.RaceTrackCell[0].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.RaceTrackCell[0].tokenButtons[0].value)
        }
        style={{ width: `${raceTrackHistory.tier}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/60 hover:bg-[#F6E7C8]/80 h-full flex justify-center items-center w-[${raceTrackHistory.tier}%]`}
      >
        <h1 className="text-xs pt-1">TIER</h1>
        <p className="absolute top-[-18px] text-[#877348]">{raceTrackHistory.tier}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.RaceTrackCell[1].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.RaceTrackCell[1].tokenButtons[0].value)
        }
        style={{ width: `${raceTrackHistory.orphelins}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/40 hover:bg-[#F6E7C8]/80 h-full flex justify-center items-center w-[${raceTrackHistory.orphelins}%]`}
      >
        <h1 className="text-xs pt-1">ORPHELINS</h1>
        <p className="absolute top-[-18px] text-[#877348]">{raceTrackHistory.orphelins}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.RaceTrackCell[2].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.RaceTrackCell[2].tokenButtons[0].value)
        }
        style={{ width: `${raceTrackHistory.voisins}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/60 hover:bg-[#F6E7C8]/80 h-full flex justify-center items-center w-[${raceTrackHistory.voisins}%]`}
      >
        <h1 className="text-xs pt-1">VOSINS</h1>
        <p className="absolute top-[-18px] text-[#877348]">{raceTrackHistory.voisins}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.RaceTrackCell[3].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.RaceTrackCell[3].tokenButtons[0].value)
        }
        style={{ width: `${raceTrackHistory.zero}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/40 hover:bg-[#F6E7C8]/80 h-full flex justify-center items-center w-[${raceTrackHistory.zero}%]`}
      >
        <h1 className="text-xs pt-1">ZERO</h1>
        <p className="absolute top-[-18px] text-[#877348]">{raceTrackHistory.zero}%</p>
      </button>
    </div>
  );
};

export default RaceTrackHistory;
