import React, { useContext, useEffect, useState } from "react";
import GameStatusContext from "../../../../context/GameStatusContext";
import socketEventsCode from "../../../../assets/data/SocketEventCode";

import "./counter.css";

interface IStatusBar {
  winNumber: number;
}

const StatusList: any = {
  SOCKET_PLACE_YOUR_BETS: {
    title: "PLACE YOUR BETS",
    bgColor: "bg-green-700/90",
  },
  SOCKET_BET_CLOSING: {
    title: "BET CLOSING",
    bgColor: "bg-orange-400/90",
  },
  SOCKET_BET_CLOSED: {
    title: "BETS CLOSED",
    bgColor: "bg-red-500/90",
  },
  SOCKET_BONUS_ROUND: {
    title: "BONUS ROUND",
    bgColor: "bg-yellow-500/90",
  },
  SOCKET_GAME_FINISHED: {
    title: "25",
    bgColor: "bg-gray-500/90",
  },
};

const StatusBar: React.FC<IStatusBar> = ({ winNumber }) => {
  const gameContext = useContext(GameStatusContext);
  const [betTimeLeft, setBetTimeLeft] = useState(15);
  const [circleDasharray, setCircleDasharray] = useState(223);

  const [remainingPathColor, setRemainingPathColor] =
    useState("text-green-600");

  const calculateTimeFraction = (leftTime: number) => {
    const rawTimeFraction = leftTime / 15;
    return rawTimeFraction - (1 / 15) * (1 - rawTimeFraction);
  };

  useEffect(() => {
    setCircleDasharray(calculateTimeFraction(betTimeLeft) * 223);
    if (betTimeLeft < 6) {
      setRemainingPathColor("text-red-600");
    } else if (betTimeLeft < 12) {
      setRemainingPathColor("text-yellow-600");
    } else {
      setRemainingPathColor("text-green-600");
    }
  }, [betTimeLeft]);

  useEffect(() => {
    if (gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) {
      setBetTimeLeft(15);
    }
  }, [gameContext.gameStatus]);

  useEffect(() => {
    const intervalId = setInterval(() => {
      setBetTimeLeft((prev) => {
        if (prev < 0) return prev;
        else return prev - 1;
      });
    }, 1000);
    return () => {
      clearInterval(intervalId);
    };
  }, []);

  return (
    <div className="bg-full text-white text-center text-bold">
      <div className="flex flex-col gap-48 justify-between py-1 text-[24px] fontSemibold leading text-white">
        {(betTimeLeft >= 0 &&
          (
            gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED ||
            gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS
          )) && (
            <div className="flex justify-center items-center w-full h-full">
              <div className="base-timer">
                <svg
                  className="base-timer__svg w-full"
                  viewBox="0 0 80 80"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <g className="base-timer__circle">
                    <circle
                      className="base-timer__path-elapsed"
                      cx="40"
                      cy="40"
                      r="35"
                    ></circle>
                    <path
                      id="base-timer-path-remaining"
                      strokeDasharray={`${circleDasharray} 283`}
                      className={`base-timer__path-remaining ${remainingPathColor}`}
                      d="
          M 40, 40
          m -35, 0
          a 35,35 0 1,0 70,0
          a 35,35 0 1,0 -70,0
        "
                    ></path>
                  </g>
                </svg>
                <span id="base-timer-label" className="base-timer__label">
                  {betTimeLeft}
                </span>
              </div>
            </div>
          )}
        {gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
          ? `Result: ${winNumber}`
          : StatusList[gameContext.gameStatus].title}
      </div>
    </div>
  );
};

export default StatusBar;
