import React, { useState } from "react";
import ReactSlider from "react-slider";

import { ReactComponent as CircleMinus } from "../../../assets/img/circle-minus.svg";
import { ReactComponent as CirclePlus } from "../../../assets/img/circle-plus.svg";

interface ISoundModalProps {
  isShowModal: boolean;
  handleSettingsModal: () => void;
}

const SoundModal: React.FC<ISoundModalProps> = ({ isShowModal, handleSettingsModal }) => {
  const [selectedVolume, setSelectedVolume] = useState<number>(0);
  const handleValueChange = (value: number) => {
    setSelectedVolume((prev: number) => {
      if (prev + value < 0 || prev + value > 100) return prev;
      else return prev + value;
    });
  };
  return (
    <div
      className={`absolute right-[280px] top-[60px] z-50 w-[310px] settings-container duration-300 ${
        isShowModal ? `opacity-90` : "opacity-0 hidden"
      }`}
    >
      <div
        className="ml-[160px] border border-x-transparent border-t-transparent border-b-[#A17B41] border-r-[10px] border-l-[10px] border-t-none border-b-[12px] w-fit h-auto"
        content=""
      ></div>
      <div className="flex gap-4 items-center border border-[#A17B41] bg-gradient-to-b from-[#161001]/90 to-[#402C00]/90 rounded-full px-4 py-2">
        <div className="w-full gap-2 flex items-center">
          <button
            onClick={() => handleValueChange(-1)}
            className="text-[#5a4617] hover:text-[#DDAE51]"
          >
            <CircleMinus className="w-6 fill-current" />
          </button>
          <ReactSlider
            className="horizontal-slider w-[160px] h-4 cursor-pointer rounded-full flex items-center"
            min={0}
            max={100}
            value={selectedVolume}
            onChange={(value) => setSelectedVolume(Number(value))}
            step={1}
            thumbActiveClassName="bg-[#DDAE51] h-4"
            thumbClassName="w-4 h-4 bg-[#DDAE51] rounded-md text-center text-[#0D0800]"
            trackClassName-0="track-0 !h-2"
            trackClassName-1="track-1 !h-2"
          />
          <button
            onClick={() => handleValueChange(1)}
            className="ms-2 text-[#5a4617] hover:text-[#DDAE51]"
          >
            <CirclePlus className="w-6 fill-current" />
          </button>
        </div>
        <div>
          <svg
            id="Group_16170"
            data-name="Group 16170"
            xmlns="http://www.w3.org/2000/svg"
            className="w-7 fill-[#9F7931] hover:fill-[#DDAE51] cursor-pointer"
            onClick={handleSettingsModal}
            viewBox="0 0 14.28 14.28"
          >
            <g id="_015-internet" data-name="015-internet">
              <path
                id="Path_1083"
                data-name="Path 1083"
                d="M13.963,5.9l-.557-.139a1.3,1.3,0,0,1-.858-2.072l.3-.492a.418.418,0,0,0-.063-.511L11.6,1.5a.418.418,0,0,0-.511-.063l-.492.3A1.3,1.3,0,0,1,8.522.875L8.383.317A.418.418,0,0,0,7.977,0H6.3A.418.418,0,0,0,5.9.317L5.759.875a1.3,1.3,0,0,1-2.072.858l-.492-.3a.418.418,0,0,0-.511.063L1.5,2.683a.418.418,0,0,0-.063.511l.3.492A1.3,1.3,0,0,1,.875,5.759L.317,5.9A.418.418,0,0,0,0,6.3V7.977a.418.418,0,0,0,.317.406l.557.139a1.3,1.3,0,0,1,.858,2.072l-.3.492A.418.418,0,0,0,1.5,11.6l1.183,1.183a.418.418,0,0,0,.511.063l.492-.3a1.3,1.3,0,0,1,2.072.858l.139.557a.418.418,0,0,0,.406.317H7.977a.418.418,0,0,0,.406-.317l.139-.557a1.3,1.3,0,0,1,2.072-.858l.492.3a.418.418,0,0,0,.511-.063L12.781,11.6a.418.418,0,0,0,.063-.511l-.3-.492a1.3,1.3,0,0,1,.858-2.072l.557-.139a.418.418,0,0,0,.317-.406V6.3a.418.418,0,0,0-.317-.406ZM7.131,10.905A3.765,3.765,0,1,1,10.914,7.14,3.78,3.78,0,0,1,7.131,10.905Z"
                transform="translate(0 0)"
              />
            </g>
            <circle
              id="Ellipse_497"
              data-name="Ellipse 497"
              cx="2.395"
              cy="2.395"
              r="2.395"
              transform="translate(4.745 4.745)"
            />
          </svg>
        </div>
      </div>
    </div>
  );
};

export default SoundModal;
