import React, { useState, useEffect, useContext } from "react";
import GameStatusContext from "../../../../../../context/GameStatusContext";
import TokenStatusContext from "../../../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../../../../context/GlobalActiveItemsContext";

import socketEventsCode from "../../../../../../assets/data/SocketEventCode";
import * as CellData from "../../../../../../assets/data/CellData";

interface IOtherHistoryProps {
  selectedHistory: Array<any>;
}

const OtherHistroy: React.FC<IOtherHistoryProps> = ({ selectedHistory }) => {
  const [halfHistory, setHalfHistory] = useState({
    firstHalf: 0,
    secondHalf: 0,
  });
  const [eventOddHistory, setEvenOddHistory] = useState({
    even: 0,
    odd: 0,
  });
  const [redBlackHistory, setRedBlackHistory] = useState({
    red: 0,
    black: 0,
  });
  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);
  
  useEffect(() => {
    calcHalfHistory();
    calcEvenOddHistory();
    calcRedBlackHistory();
  }, [selectedHistory]);

  const calcHalfHistory = () => {
    const newHalfHistory = {
      firstHalf: 0,
      secondHalf: 0,
    };
    selectedHistory.forEach((history) => {
      const { winNumber } = history;
      if (
        CellData.HighLowCell[0].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newHalfHistory.firstHalf++;
      else if (
        CellData.HighLowCell[1].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newHalfHistory.secondHalf++;
    });
    setHalfHistory({
      firstHalf: Number(
        ((100 * newHalfHistory.firstHalf) / selectedHistory.length).toFixed(0)
      ),
      secondHalf: Number(
        ((100 * newHalfHistory.secondHalf) / selectedHistory.length).toFixed(0)
      ),
    });
  };

  const calcEvenOddHistory = () => {
    const newEvenOddHistory = {
      even: 0,
      odd: 0,
    };
    selectedHistory.forEach((history) => {
      const { winNumber } = history;
      if (
        CellData.EvenOddCell[0].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newEvenOddHistory.even++;
      else if (
        CellData.EvenOddCell[1].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newEvenOddHistory.odd++;
    });
    setEvenOddHistory({
      even: Number(
        ((100 * newEvenOddHistory.even) / selectedHistory.length).toFixed(0)
      ),
      odd: Number(
        ((100 * newEvenOddHistory.odd) / selectedHistory.length).toFixed(0)
      ),
    });
  };
  const calcRedBlackHistory = () => {
    const newRedBlackHistory = {
      red: 0,
      black: 0,
    };
    selectedHistory.forEach((history) => {
      const { winNumber } = history;
      if (CellData.ColorCell[0].tokenButtons[0].value.indexOf(winNumber) !== -1)
        newRedBlackHistory.red++;
      else if (
        CellData.ColorCell[1].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newRedBlackHistory.black++;
    });
    setRedBlackHistory({
      red: Number(
        ((100 * newRedBlackHistory.red) / selectedHistory.length).toFixed(0)
      ),
      black: Number(
        ((100 * newRedBlackHistory.black) / selectedHistory.length).toFixed(0)
      ),
    });
  };

  const handleMouseEnterEvent = (value: Array<number>) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
      globalActiveItemsContext.setGlobalActiveItems(value);
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (value: Array<number>) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      if (
        Number(balanceStatusContext.balanceStatus.totalBalance) >=
        Number(tokenStatusContext.tokenStatus.currentToken)
      ) {
        const newSelectedToken: Array<any> =
          tokenStatusContext.tokenStatus.selectedToken;

        newSelectedToken.push([
          {
            value: value,
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          },
        ]);
        tokenStatusContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceStatusContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance:
            prev.totalBalance -
            Number(tokenStatusContext.tokenStatus.currentToken),
          betBalance:
            prev.betBalance +
            Number(tokenStatusContext.tokenStatus.currentToken),
        }));
      }
    }
  };

  return (
    <div className="grid grid-cols-6 h-[120px] w-full px-4 gap-1 text-sm">
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.HighLowCell[0].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.HighLowCell[0].tokenButtons[0].value)
        }
        className="transition-colors duration-300 bg-[#F6E7C8]/20 hover:bg-[#F6E7C8]/60 text-black/80 flex justify-center relative"
        style={{ height: `${halfHistory.firstHalf}%` }}
      >
        <h1 className="text-xs pt-1">1 - 18</h1>
        <p className="absolute top-[-18px] text-[#877348]">{halfHistory.firstHalf}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.EvenOddCell[0].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.EvenOddCell[0].tokenButtons[0].value)
        }
        className="transition-colors duration-300 bg-[#F6E7C8]/20 hover:bg-[#F6E7C8]/60 text-black/80 flex justify-center relative"
        style={{ height: `${eventOddHistory.even}%` }}
      >
        <h1 className="text-xs pt-1">EVEN</h1>
        <p className="absolute top-[-18px] text-[#877348]">{eventOddHistory.even}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.ColorCell[0].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.ColorCell[0].tokenButtons[0].value)
        }
        className="transition-colors duration-300 bg-[#F6E7C8]/20 hover:bg-[#F6E7C8]/60 text-black/80 flex justify-center relative"
        style={{ height: `${redBlackHistory.red}%` }}
      >
        <svg xmlns="http://www.w3.org/2000/svg" className="w-full h-fit p-1" viewBox="0 0 45.488 12.569">
          <path id="Polygon_97" data-name="Polygon 97" d="M6.285,0l6.285,22.744H0Z" transform="translate(0 12.569) rotate(-90)" fill="red" opacity="0.44"/>
          <path id="Path_3736" data-name="Path 3736" d="M6.285,0l6.285,22.744L1.114,23.763,0,22.744Z" transform="translate(45.488 0) rotate(90)" fill="red" opacity="0.44"/>
        </svg>
        <p className="absolute top-[-18px] text-[#877348]">{redBlackHistory.red}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.ColorCell[1].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.ColorCell[1].tokenButtons[0].value)
        }
        className="transition-colors duration-300 bg-[#F6E7C8]/20 hover:bg-[#F6E7C8]/60 text-black/80 flex justify-center relative"
        style={{ height: `${redBlackHistory.black}%` }}
      >
        <svg xmlns="http://www.w3.org/2000/svg" className="w-full h-fit p-1" viewBox="0 0 45.488 12.569">
          <path id="Polygon_98" data-name="Polygon 98" d="M6.285,0l6.285,22.744H0Z" transform="translate(0 12.569) rotate(-90)" opacity="0.58"/>
          <path id="Path_3737" data-name="Path 3737" d="M6.285,0l6.285,22.744L1.114,23.763,0,22.744Z" transform="translate(45.488 0) rotate(90)" opacity="0.58"/>
        </svg>
        <p className="absolute top-[-18px] text-[#877348]">{redBlackHistory.black}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.EvenOddCell[1].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.EvenOddCell[1].tokenButtons[0].value)
        }
        className="transition-colors duration-300 bg-[#F6E7C8]/20 hover:bg-[#F6E7C8]/60 text-black/80 flex justify-center relative"
        style={{ height: `${eventOddHistory.odd}%` }}
      >
        <h1 className="text-xs pt-1">ODD</h1>
        <p className="absolute top-[-18px] text-[#877348]">{eventOddHistory.odd}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.HighLowCell[1].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.HighLowCell[1].tokenButtons[0].value)
        }
        className="transition-colors duration-300 bg-[#F6E7C8]/20 hover:bg-[#F6E7C8]/60 text-black/80 flex justify-center relative"
        style={{ height: `${halfHistory.firstHalf}%` }}
      >
        <h1 className="text-xs pt-1">19 - 36</h1>
        <p className="absolute top-[-18px] text-[#877348]">{halfHistory.secondHalf}%</p>
      </button>
    </div>
  );
};

export default OtherHistroy;
