import React, { useState, useEffect, useContext } from "react";

import GameStatusContext from "../../../../context/GameStatusContext";
import TokenStatusContext from "../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../../context/GlobalActiveItemsContext";

import socketEventsCode from "../../../../assets/data/SocketEventCode";

import * as CellData from "../../../../assets/data/CellData";
interface IColumnHistoryProps {
  selectedHistory: Array<any>;
}

const ColumnHistory: React.FC<IColumnHistoryProps> = ({ selectedHistory }) => {
  const [columnHistory, setColumnHistory] = useState({
    first: 0,
    second: 0,
    third: 0,
  });
  
  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);
  
  const calcColumnHistory = () => {
    const newColumnHistory = {
      first: 0,
      second: 0,
      third: 0,
    };
    selectedHistory.forEach((history) => {
      const { winNumber } = history;
      if (
        CellData.ColGroupCell[0].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newColumnHistory.first++;
      else if (
        CellData.ColGroupCell[1].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newColumnHistory.second++;
      else if (
        CellData.ColGroupCell[2].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newColumnHistory.third++;
    });
    setColumnHistory({
      first: Number(
        ((100 * newColumnHistory.first) / selectedHistory.length).toFixed(0)
      ),
      second: Number(
        ((100 * newColumnHistory.second) / selectedHistory.length).toFixed(0)
      ),
      third: Number(
        ((100 * newColumnHistory.third) / selectedHistory.length).toFixed(0)
      ),
    });
  };
  useEffect(() => {
    calcColumnHistory();
  }, [selectedHistory]);

  const handleMouseEnterEvent = (value: Array<number>) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
      globalActiveItemsContext.setGlobalActiveItems(value);
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (value: Array<number>) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      if (
        Number(balanceStatusContext.balanceStatus.totalBalance) >=
        Number(tokenStatusContext.tokenStatus.currentToken)
      ) {
        const newSelectedToken: Array<any> =
          tokenStatusContext.tokenStatus.selectedToken;

        newSelectedToken.push([
          {
            value: value,
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          },
        ]);
        tokenStatusContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceStatusContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance:
            prev.totalBalance -
            Number(tokenStatusContext.tokenStatus.currentToken),
          betBalance:
            prev.betBalance +
            Number(tokenStatusContext.tokenStatus.currentToken),
        }));
      }
    }
  };
  return (
    <div className="flex px-4 w-full h-[40px] text-sm gap-1">
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.ColGroupCell[0].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.ColGroupCell[0].tokenButtons[0].value)
        }
        style={{ width: `${columnHistory.first}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/60 hover:bg-[#F6E7C8]/80 h-full flex justify-center items-center w-[${columnHistory.first}%]`}
      >
        <h1>1ST COLUMN</h1>
        <p className="absolute top-[-18px] text-[#877348]">{columnHistory.first}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.ColGroupCell[1].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.ColGroupCell[1].tokenButtons[0].value)
        }
        style={{ width: `${columnHistory.second}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/40 hover:bg-[#F6E7C8]/80 h-full flex justify-center items-center w-[${columnHistory.second}%]`}
      >
        <h1>2ND COLUMN</h1>
        <p className="absolute top-[-18px] text-[#877348]">{columnHistory.second}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.ColGroupCell[2].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.ColGroupCell[2].tokenButtons[0].value)
        }
        style={{ width: `${columnHistory.third}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/60 hover:bg-[#F6E7C8]/80 h-full flex justify-center items-center w-[${columnHistory.third}%]`}
      >
        <h1>3RD COLUMN</h1>
        <p className="absolute top-[-18px] text-[#877348]">{columnHistory.third}%</p>
      </button>
    </div>
  );
};

export default ColumnHistory;
