import React, { useEffect, useContext } from "react";
import { RaceTrackCell } from "../../../../assets/data/CellData";
import TokenStatusContext from "../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../context/BalanceStatusContext";
import GameStatusContext from "../../../../context/GameStatusContext";
import GlobalActiveItemsContext from "../../../../context/GlobalActiveItemsContext";
import NumberMask from "./NumberMask";

import socketEventsCode from "../../../../assets/data/SocketEventCode";

interface IRaceTrackMaskProps {
  setActiveTokenNumbers: (value: Array<number>) => void;
  raceTrackCount: number;
}
const RaceTrackMask: React.FC<IRaceTrackMaskProps> = ({
  setActiveTokenNumbers,
  raceTrackCount,
}) => {
  const tokenContext = useContext(TokenStatusContext);
  const balanceContext = useContext(BalanceStatusContext);
  const gameContext = useContext(GameStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext)

  useEffect(() => {
    const newSelectedToken: any[] = [];
    tokenContext.tokenStatus.selectedToken.forEach((tokenList) => {
      tokenList.forEach((token: any) => {
        RaceTrackCell.forEach((cell) => {
          if (cell.tokenButtons[0].value.toString() === token.value.toString())
            newSelectedToken.push(token);
        });
      });
    });
  }, [tokenContext.tokenStatus]);

  const handleMouseEnter = (value: Array<number>) => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      setActiveTokenNumbers(value);
      globalActiveItemsContext.setGlobalActiveItems(value);
    }
  };

  const handleMouseLeave = () => {
      setActiveTokenNumbers([]);
      globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (id: number, valueList: Array<number>) => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      const estimateAmount =
        valueList.length * tokenContext.tokenStatus.currentToken;

      if (Number(balanceContext.balanceStatus.totalBalance) >= estimateAmount) {
        const newSelectedToken: Array<any> =
          tokenContext.tokenStatus.selectedToken;
        const selectedToken: Array<any> = [];
        valueList.forEach((value) => {
          selectedToken.push({
            value: [value],
            amount: tokenContext.tokenStatus.currentToken,
            type: "mainboard",
          });
        });
        newSelectedToken.push(selectedToken);
        tokenContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceContext.setBalanceStatus({
          totalBalance:
            balanceContext.balanceStatus.totalBalance - estimateAmount,
          betBalance: balanceContext.balanceStatus.betBalance + estimateAmount,
        });
      }
    }
  };
  return (
    <g id="Mask" transform="translate(1519.857 -6205.5)">
      <NumberMask
        mouseEnter={handleMouseEnter}
        mouseLeave={handleMouseLeave}
        raceTrackCount={raceTrackCount}
      />
      <g id="INNER" transform="translate(-1563.857 6279.5)">
        <path
          onMouseEnter={() =>
            handleMouseEnter(RaceTrackCell[0].tokenButtons[0].value)
          }
          onMouseLeave={handleMouseLeave}
          onClick={() =>
            handleTokenSelect(
              RaceTrackCell[0].tokenButtons[0].id,
              RaceTrackCell[0].tokenButtons[0].value
            )
          }
          id="TIER-2"
          data-name="TIER"
          d="M1593.292,875.9l-92.04-.1s-26.653-5.513-26.633-29.959,26.633-30.656,26.633-30.656h47.685Z"
          opacity="0"
          fill="#222"
        />
        <path
          onMouseEnter={() =>
            handleMouseEnter(RaceTrackCell[1].tokenButtons[0].value)
          }
          onMouseLeave={handleMouseLeave}
          onClick={() =>
            handleTokenSelect(
              RaceTrackCell[1].tokenButtons[0].id,
              RaceTrackCell[1].tokenButtons[0].value
            )
          }
          id="ORPHENLINS"
          d="M1550.565,814.872h116.972v61.015h-72.223Z"
          transform="translate(0 0.103)"
          opacity="0"
          fill="#222"
        />
        <path
          onMouseEnter={() =>
            handleMouseEnter(RaceTrackCell[2].tokenButtons[0].value)
          }
          onMouseLeave={handleMouseLeave}
          onClick={() =>
            handleTokenSelect(
              RaceTrackCell[2].tokenButtons[0].id,
              RaceTrackCell[2].tokenButtons[0].value
            )
          }
          id="VOISINS-2"
          data-name="VOISINS"
          d="M1668.825,814.975h114.341s-22.867,5.144-22.706,31.141,22.706,29.873,22.706,29.873H1668.825Z"
          transform="translate(0)"
          opacity="0"
          fill="#222"
        />
        <rect
          onMouseEnter={() =>
            handleMouseEnter(RaceTrackCell[3].tokenButtons[0].value)
          }
          onMouseLeave={handleMouseLeave}
          onClick={() =>
            handleTokenSelect(
              RaceTrackCell[3].tokenButtons[0].id,
              RaceTrackCell[3].tokenButtons[0].value
            )
          }
          id="ZERO-2"
          data-name="ZERO"
          width="79"
          height="63"
          rx="31.5"
          transform="translate(1761 814)"
          opacity="0"
          fill="#222"
        />
      </g>
    </g>
  );
};

export default RaceTrackMask;
