import React from 'react';

import { ReactComponent as PlaceBetIcon } from "../../../../assets/img/bet-limit.svg";
import { ReactComponent as EmptyFavouriteIcon } from "../../../../assets/img/empty-favourite.svg";

const Favourite:React.FC = () => {
  return (
    <div className='w-full p-4 h-full flex flex-col'>
      <div className='w-full text-center pt-6'>
        <h1 className='flex gap-4 items-center justify-center w-full py-2 text-lg text-[#9E7512] border-b border-[#DFAE51]/30 font-extrabold'>
          <PlaceBetIcon className='h-8 ' />
          Place Bets and Save
        </h1>
        <p className='text-[#946E11] py-2 text-sm'>When ready, Place your Bet and then Save it!</p>
      </div>
      <div className='flex justify-center items-center h-full '>
        <EmptyFavouriteIcon className='w-[300px] opacity-80' />
      </div>
    </div>
  )
}

export default Favourite