import { useState, useEffect, useContext, useMemo } from "react";
import * as d3 from "d3";
import GameStatusContext from "../../../../../../context/GameStatusContext";
import TokenStatusContext from "../../../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../../../../context/GlobalActiveItemsContext";
import socketEventsCode from "../../../../../../assets/data/SocketEventCode";

const MARGIN = 0;
const BAR_PADDING = 0.05;

type CircularBarplotProps = {
  width: number;
  height: number;
  data: { name: string; value: number }[];
  maxCount: number;
  sortedBetHistory: { name: string; value: number }[];
};

const redData = [
  1, 3, 5, 7, 9, 12, 14, 16, 18, 19, 21, 23, 25, 27, 30, 32, 34, 36,
];
const blackData = [
  2, 4, 6, 8, 10, 11, 13, 15, 17, 20, 22, 24, 26, 28, 29, 31, 33, 35,
];

const getClassAttribute = (winNumber: number) => {
  let className = "";
  if (redData.indexOf(winNumber) !== -1) className = "fill-red-600";
  else if (blackData.indexOf(winNumber) !== -1) className = "fill-white";
  else className = "fill-green-600";
  return className;
};

export const CircularBarplot = ({
  width,
  height,
  data,
  maxCount,
  sortedBetHistory,
}: CircularBarplotProps) => {
  const innerRadius = 10;
  const outerRadius = Math.min(width, height) / 2 - 40;
  const [hotData, setHotData] = useState<{ name: string; value: number }[]>([]);
  const [coldData, setColdData] = useState<{ name: string; value: number }[]>(
    []
  );
  const [hoverNumber, setHoverNumber] = useState<number>(-1);

  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);

  const handleMouseEnterEvent = (value: number) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
    globalActiveItemsContext.setGlobalActiveItems([value]);
    setHoverNumber(value);
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
    setHoverNumber(-1);
  };

  const handleTokenSelect = (value: number) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      if (
        Number(balanceStatusContext.balanceStatus.totalBalance) >=
        Number(tokenStatusContext.tokenStatus.currentToken)
      ) {
        const newSelectedToken: Array<any> =
          tokenStatusContext.tokenStatus.selectedToken;

        newSelectedToken.push([
          {
            value: [value],
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          },
        ]);
        tokenStatusContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceStatusContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance:
            prev.totalBalance -
            Number(tokenStatusContext.tokenStatus.currentToken),
          betBalance:
            prev.betBalance +
            Number(tokenStatusContext.tokenStatus.currentToken),
        }));
      }
    }
  };

  useEffect(() => {
    setHotData(sortedBetHistory.slice(0, 5));
    setColdData(sortedBetHistory.slice(32, 37));
  }, [sortedBetHistory]);

  const getBetDataClassAttribute = (winNumber: string) => {
    if (hotData.filter((history) => history.name === winNumber).length > 0) {
      return "fill-[red]/80";
    } else if (
      coldData.filter((history) => history.name === winNumber).length > 0
    ) {
      return "fill-[blue]/80";
    } else return "fill-[#493814]";
  };

  const groups = data.map((d) => d.name);
  const xScale = useMemo(() => {
    return d3
      .scaleBand()
      .domain(groups)
      .range([0, 2 * Math.PI])
      .padding(BAR_PADDING);
  }, [data, height, width]);

  const yScale = useMemo(() => {
    const [min, max] = d3.extent(data.map((d) => d.value));
    return d3
      .scaleRadial()
      .domain([0, max || 10])
      .range([innerRadius, outerRadius]);
  }, [data, width, height]);

  // Build the shapes
  const arcPathGenerator = d3.arc();
  const allShapes = data.map((group, i) => {
    const path = arcPathGenerator({
      innerRadius: innerRadius,
      outerRadius: yScale(group.value),
      startAngle: xScale(group.name) || 0,
      endAngle: (xScale(group.name) || 0) + xScale.bandwidth(),
    });

    return (
      <g
        key={i}
        className={`${
          hoverNumber === Number(data[i].name) ? "opacity-100" : "opacity-50"
        } transition-all duration-300`}
      >
        <path
          d={path || ""}
          className={getBetDataClassAttribute(data[i].name)}
          rx={1}
        />
      </g>
    );
  });
  const backShapes = data.map((group, i) => {
    const path = arcPathGenerator({
      innerRadius: innerRadius,
      outerRadius: yScale(maxCount),
      startAngle: xScale(group.name) || 0,
      endAngle: (xScale(group.name) || 0) + xScale.bandwidth(),
    });

    const barAngle = (xScale(group.name) || 0) + xScale.bandwidth() / 2; // (in Radian)
    // const turnLabelUpsideDown = (barAngle + Math.PI) % (2 * Math.PI) < Math.PI;
    const labelRotation = (barAngle * 180) / Math.PI - 3; // (convert radian to degree)
    const labelXTranslation = yScale(maxCount) + 10;
    const labelTransform =
      "rotate(" +
      labelRotation +
      ")" +
      ",translate(0, " +
      -labelXTranslation +
      ")";

    return (
      <g key={i}>
        <path
          d={path || ""}
          opacity={0.7}
          stroke="white"
          fill="transparent"
          fillOpacity={0.3}
          strokeWidth={0.3}
          rx={1}
        />
        <g transform={labelTransform}>
          <text
            textAnchor={"start"}
            // textAnchor={turnLabelUpsideDown ? "end" : "start"}
            alignmentBaseline="middle"
            fontSize={14}
            className={getClassAttribute(Number(group.name))}
            fill="currentColor"
            // transform={turnLabelUpsideDown ? "rotate(180)" : "rotate(0)"}
          >
            {group.name}
          </text>
        </g>
      </g>
    );
  });

  const outBackShapes = data.map((group, i) => {
    const path = arcPathGenerator({
      innerRadius: innerRadius,
      outerRadius: yScale(maxCount * 1.6),
      startAngle: xScale(group.name) || 0,
      endAngle: (xScale(group.name) || 0) + xScale.bandwidth(),
    });

    return (
      <g
        key={i}
        className="cursor-pointer"
        onMouseEnter={() => handleMouseEnterEvent(Number(data[i].name))}
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() => handleTokenSelect(Number(data[i].name))}
      >
        <path
          d={path || ""}
          opacity={0.7}
          stroke="white"
          fill="transparent"
          fillOpacity={0.3}
          strokeWidth={0.3}
          rx={1}
        />
      </g>
    );
  });

  return (
    <div>
      <svg width={width} height={height}>
        <g
          transform={
            "translate(" +
            (width / 2 + MARGIN) +
            "," +
            (height / 2 + MARGIN) +
            ")"
          }
        >
          {allShapes}
          {backShapes}
          {outBackShapes}
        </g>
      </svg>
    </div>
  );
};
