import React, { useContext, useEffect, useState } from "react";

import Favourite from "./Favourite";
import Special from "./Special";
import StraightUp from "./StraightUp";

import { ReactComponent as ModalCloseIcon } from "../../../assets/img/modal-close.svg";
import { ReactComponent as FavouriteIcon } from "../../../assets/img/favourite.svg";

import GameHistoryContext from "../../../context/HistoryContext";

interface IFavouriteModalProps {
  isShowModal: boolean;
  handleClose: () => void;
}

const FavouiteModal: React.FC<IFavouriteModalProps> = ({
  isShowModal,
  handleClose,
}) => {
  const tabNames = ["FAVOURITE", "SPECIAL", "STRAIGHT"];

  const [selectedTab, setSelectedTab] = useState<number>(0);

  const tabContent = [<Favourite />, <Special />, <StraightUp />];
  return (
    <div
      className={`absolute right-[20px] bottom-[-510px] z-50 w-[500px] settings-container duration-300 ${
        isShowModal ? "-translate-y-[600px] " : "translate-y-[280px]"
      }`}
    >
      <div className="bg-[#997928] flex justify-between px-4 py-1 relative rounded-t-md text-md">
        <div className="flex gap-2 items-center relative">
          <FavouriteIcon className="w-4" />
          <h1 className="text-black text-md font-bold">FAVOURITE BETS</h1>
          <h1 className="absolute right-0 text-black text-md font-bold">
            FAVOURITE BETS
          </h1>
        </div>
        <button
          onClick={handleClose}
          className="h-full my-auto text-lg text-black/60 hover:text-black"
        >
          <ModalCloseIcon className="w-5 fill-current" />
        </button>
      </div>
      <div className="bg-gradient-to-b from-[#1A1300]/95 to-[#352404]/95 h-full ">
        <div className="flex justify-center gap-4 pt-6">
          {tabNames.map((name, index) => (
            <button
              key={index}
              className={` rounded-md py-2 text-[#32230A] w-[120px] font-bold ${
                selectedTab === index
                  ? "bg-[#DDAE51] text-[#32230A]"
                  : "bg-transparent hover:bg-[#DDAE51]/50 text-[#C79D48] border-[#C79D48] border-2 "
              }`}
              onClick={() => setSelectedTab(index)}
            >
              {name}
            </button>
          ))}
        </div>
        <div className="w-full h-[480px] ">{tabContent[selectedTab]}</div>
      </div>
      <div className="bg-[#997928]/70 flex justify-between px-4 py-1 relative text-xl rounded-b-md"></div>
      <div
        className="ml-[140px] border border-x-transparent border-b-transparent border-t-[#997928]/70 border-r-[10px] border-l-[10px] border-b-none border-t-[10px] w-fit h-auto"
        content=""
      ></div>
    </div>
  );
};

export default FavouiteModal;
