import React from "react";

import MainBoard from "../../../components/MainBoard";
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";

const GamePanel: React.FC<any> = ({ lastMessage, sendMessage }) => {
  return (
    <div>
      <Header lastMessage={lastMessage} sendMessage={sendMessage} />
      <MainBoard lastMessage={lastMessage} sendMessage={sendMessage} />
      <Footer />
    </div>
  );
};

export default GamePanel;
