import React, { useState, useEffect, useContext } from "react";
import clsx from "classnames";

import GameHistoryContext from "../../../context/HistoryContext";
import GameStatusContext from "../../../context/GameStatusContext";
import TokenStatusContext from "../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../context/GlobalActiveItemsContext";
import socketEventsCode from "../../../assets/data/SocketEventCode";

export const RecentHistory: React.FC<any> = (props) => {
  const gameHistoryContext = useContext(GameHistoryContext);
  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);

  const [gameHistory, setGameHistory] = useState<Array<any>>([]);

  useEffect(() => {
    setGameHistory(gameHistoryContext.gameHistory.slice(0, props.count));
  }, [gameHistoryContext.gameHistory]);

  const redData = [
    1, 3, 5, 7, 9, 12, 14, 16, 18, 19, 21, 23, 25, 27, 30, 32, 34, 36,
  ];
  const blackData = [
    2, 4, 6, 8, 10, 11, 13, 15, 17, 20, 22, 24, 26, 28, 29, 31, 33, 35,
  ];

  const handleMouseEnterEvent = (value: number) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
      globalActiveItemsContext.setGlobalActiveItems([value]);
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (value: number) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      if (
        Number(balanceStatusContext.balanceStatus.totalBalance) >=
        Number(tokenStatusContext.tokenStatus.currentToken)
      ) {
        const newSelectedToken: Array<any> =
          tokenStatusContext.tokenStatus.selectedToken;

        newSelectedToken.push([
          {
            value: [value],
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          },
        ]);
        tokenStatusContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceStatusContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance:
            prev.totalBalance -
            Number(tokenStatusContext.tokenStatus.currentToken),
          betBalance:
            prev.betBalance +
            Number(tokenStatusContext.tokenStatus.currentToken),
        }));
      }
    }
  };

  const getClassAttribute = (
    index: number,
    winNumber: number,
    luckyNumbers: Array<any>
  ) => {
    let className = "";
    const luckyNumber = luckyNumbers?.filter(
      (item) => item.number === winNumber
    );
    className = "bg-[#E9C84D]/60 text-white";
    if (luckyNumber?.length > 0) {
      className =
        "bg-gradient-to-b from-[#755D1D]/95 to-[#AC8621]/95 text-white w-8 h-8";
    } else if (index === 0) {
      if (redData.indexOf(winNumber) !== -1)
        className = "bg-[red]/60 border text-white hover:text-white w-8 h-8";
      else if (blackData.indexOf(winNumber) !== -1)
        className = "bg-black/60  border text-white hover:text-white w-8 h-8";
      else className = "bg-green/60 border text-white hover:text-white w-8 h-8";
    } else {
      if (redData.indexOf(winNumber) !== -1)
        className = "text-[red] bg-transparent w-8 h-7";
      else if (blackData.indexOf(winNumber) !== -1)
        className = "text-white bg-transparent w-8 h-7";
      else className = "text-green-600 bg-transparent w-8 h-7";
    }
    return className;
  };

  const checkLuckyNumber = (luckyNumber: any, winNumber: number) => {
    const filterNumbers = luckyNumber?.filter(
      (lucky: any) => lucky.number === winNumber
    );
    return filterNumbers?.length > 0 ? filterNumbers[0] : null;
  };

  return (
    <>
      {gameHistory.length > 0 && (
        <div className="pt-2">
          <div className="flex relative justify-between bg-[#020200] h-7 items-center w-min-[300px] w-full">
            {gameHistory.map((history: any, index: number) => (
              <button
                onMouseEnter={() => handleMouseEnterEvent(history.winNumber)}
                onMouseLeave={() => handleMouseLeaveEvent()}
                onClick={() => handleTokenSelect(history.winNumber)}
                className={clsx(
                  "relative flex justify-center items-center text-sm rounded-md hover:bg-[#4D3C03] transition-colors duration-300",
                  getClassAttribute(
                    index,
                    history.winNumber,
                    history.luckyNumber
                  )
                )}
                key={index}
              >
                {history.winNumber}
                <p className="absolute bottom-[-12px] font-bold text-[#F2B700]">
                  {checkLuckyNumber(history.luckyNumber, history.winNumber)
                    ? "x" +
                      checkLuckyNumber(history.luckyNumber, history.winNumber)
                        ?.multiple
                    : null}
                </p>
              </button>
            ))}
          </div>
        </div>
      )}
    </>
  );
};
