import React, { useState, useContext } from "react";

import AuthContext from "./../../context/AuthContext";
interface ISignInProps {
  handleClose: () => void;
}

const SingIn: React.FC<ISignInProps> = ({ handleClose }) => {
  const authContext = useContext(AuthContext);
  const [userId, setUserId] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  const [error, setError] = useState<string>("");
  const handleSubmit = () => {
    if (password === "123qwe!!!") {
      authContext.setUserId(userId);
      handleClose();
    } else {
      setError("Wrong password!");
    }
  };
  return (
    <div className="fixed left-0 top-0 z-50 h-full w-full overflow-y-auto overflow-x-hidden outline-none bg-black/80">
      <div className="pointer-events-none relative w-auto transition-all duration-300 ease-in-out mx-auto max-w-[500px] top-[300px]">
        <div className="pointer-events-auto relative flex w-full flex-col rounded-md  bg-clipPadding text-current shadow-lg outline-none border  bg-gray-900/95">
          <div className="flex flex-shrink-0 items-center justify-between rounded-t-md border-b-2 border-neutral-100 p-4 border-opacity-50">
            <h5
              className="text-xl font-medium leading-normal text-neutral-200"
              id="exampleModalLabel"
            >
              Sign In
            </h5>

            {/* <button
              type="button"
              className="box-content rounded-none border-none hover:no-underline hover:opacity-75 focus:opacity-100 focus:shadow-none focus:outline-none text-white"
              data-te-modal-dismiss
              aria-label="Close"
              onClick={handleClose}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth="1.5"
                stroke="currentColor"
                className="h-6 w-6"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </button> */}
          </div>

          <div className="relative flex-auto p-4" data-te-modal-body-ref>
            <h1 className="text-red-600 text-2xl">{error}</h1>
            <div className="">
              <label
                htmlFor="email"
                className="block mb-2 text-sm font-medium text-white"
              >
                UserID
              </label>
              <input
                id="email"
                value={userId}
                onChange={(e) => setUserId(e.target.value)}
                className="bg-gray-50 border my-4 border-gray-300 text-gray-900 text-sm font-medium rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="luckyboy2023"
                required
              />
              <label
                htmlFor="email"
                className="block mb-2 text-sm font-medium text-white"
              >
                Password
              </label>
              <input
                id="password"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="bg-gray-50 border border-gray-300 text-black text-sm leading-[0px] font-medium rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="luckyboy2023"
                required
              />
            </div>
          </div>

          <div className="flex flex-shrink-0 flex-wrap items-center justify-end rounded-b-md p-4">
            {/* <button
              type="button"
              className="inline-block rounded bg-primary-100 px-6 pb-2 pt-2.5 text-xs text-white font-medium uppercase leading-normal text-primary-700 transition duration-150 ease-in-out  "
              data-te-modal-dismiss
              data-te-ripple-init
              data-te-ripple-color="light"
              onClick={handleClose}
            >
              Close
            </button> */}
            <button
              type="button"
              className="ml-1 inline-block rounded bg-primary px-6 pb-2 pt-2.5 text-xs font-medium uppercase leading-normal text-white  border border-white transition duration-150 ease-in-out "
              data-te-ripple-init
              data-te-ripple-color="light"
              onClick={handleSubmit}
            >
              Sign In
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SingIn;
