import React, { useContext } from "react";
import blanceStatusContext from "../../context/BalanceStatusContext";

import RoulletWhell from "../../assets/img/coin.svg";
import CoinSelect from "./CoinSelect";
import FooterMenu from "./Menu";

const Footer: React.FC = () => {
  const { balanceStatus } = useContext(blanceStatusContext);
  return (
    <div className="absolute bottom-0 w-full h-[90px] bg-[#000000] px-12">
      <div className="flex items-center h-full justify-between">
        <div className="flex flex-row gap-4 justify-around">
          <div className="text-center w-[150px] pt-2 rounded-full bg-transparent border border-[#958963] leading-4">
            <p className="text-md text-[#EABB25] font-black">
              {balanceStatus.totalBalance.toFixed(2)} €
            </p>
            <h2 className="text-[10px] text-gray-400">BALANCE</h2>
          </div>

          <div className="text-center w-[150px] pt-2 rounded-full bg-transparent border border-[#958963] leading-4 relative overflow-hidden">
            <img
              src={RoulletWhell}
              className="absolute w-8 h-8 left-[10px] transform -translate-x-1/3 translate-y-0/3"
              alt="coin"
            />
            <p className="text-md text-[#EABB25] font-black">
              {balanceStatus.betBalance.toFixed(2)} €
            </p>
            <h2 className="text-[10px] text-gray-400">TOTAL BET</h2>
          </div>
        </div>
        <CoinSelect />
        <FooterMenu />
      </div>
    </div>
  );
};

export default Footer;
