import React, { useRef, useState } from "react";
import ReactHlsPlayer from 'react-hls-player';

const VideoBackground: React.FC = () => {
  const [videoVisible, setVideoVisible] = useState<boolean>(true);
  const hlsPlayerRef = useRef(null);

  const handleOnline = () => setVideoVisible(true);

  const handleOffline = () => setVideoVisible(false);

  return (
    <div
      className={`absolute top-0 left-0 z-[-1] ${videoVisible ? "" : "hidden"}`}
    >
      <ReactHlsPlayer
        playerRef={hlsPlayerRef}
        autoPlay={true}
        controls={false}
        src="https://5eefe582b5018010.mediapackage.eu-north-1.amazonaws.com/out/v1/8ce00b884c18460a8ab2242c4b7ab944/index.m3u8"
        width={1920}
        height={1080}
        className="z-40 w-[1920px] h-[1080px] flex items-center bg-[black]"
        hlsConfig={{
          maxLoadingDelay: 4,
          minAutoBitrate: 0,
          lowLatencyMode: true,
        }}
      />
    </div>
  );
};

export default VideoBackground;
