import React from "react";
import RowHistory from "./RowHistory";
import ColumnHistory from "./ColumnHistory";
import RaceTrackHistory from "./RaceTrackHistory";
import OtherHistroy from "./OtherHistroy";
interface IAdvancedProps {
  selectedHistory: Array<any>;
}
const Advanced: React.FC<IAdvancedProps> = ({ selectedHistory }) => {

  return (
    <div className="flex gap-12 flex-col pt-8 font-bold">
      <RowHistory selectedHistory={selectedHistory} />
      <ColumnHistory selectedHistory={selectedHistory} />
      <RaceTrackHistory selectedHistory={selectedHistory} />
      <OtherHistroy selectedHistory={selectedHistory} />
    </div>
  );
};

export default Advanced;
