import { createContext } from "react";

export type IBalanceStatusContext = {
  balanceStatus: { totalBalance: number, betBalance: number};
  setBalanceStatus: (balanceStatus: any) => void;
}

const BalanceStatusContext = createContext<IBalanceStatusContext>({
  balanceStatus: { totalBalance: 5000, betBalance: 0},
  setBalanceStatus: () => {},
});

export default BalanceStatusContext;
