import React, { useContext } from "react";
import TokenStatusContext from "../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../context/BalanceStatusContext";

import { ReactComponent as Coin1 } from "../../../assets/img/coin/coin1.svg";
import { ReactComponent as Coin2 } from "../../../assets/img/coin/coin2.svg";
import { ReactComponent as Coin3 } from "../../../assets/img/coin/coin3.svg";
import { ReactComponent as Coin4 } from "../../../assets/img/coin/coin4.svg";
import { ReactComponent as Coin5 } from "../../../assets/img/coin/coin5.svg";
import { ReactComponent as Coin6 } from "../../../assets/img/coin/coin6.svg";
import { ReactComponent as Coin7 } from "../../../assets/img/coin/coin7.svg";

import GameStatusContext from "../../../context/GameStatusContext";
import socketEventsCode from "../../../assets/data/SocketEventCode";

const coins = [
  {
    amount: 0.25,
    image: <Coin1 />,
  },
  {
    amount: 0.5,
    image: <Coin2 />,
  },
  {
    amount: 1,
    image: <Coin3 />,
  },
  {
    amount: 5,
    image: <Coin4 />,
  },
  {
    amount: 25,
    image: <Coin5 />,
  },
  {
    amount: 100,
    image: <Coin6 />,
  },
  {
    amount: 500,
    image: <Coin7 />,
  },
];

const CoinSelect: React.FC = () => {
  const tokenContext = useContext(TokenStatusContext);
  const balanceContext = useContext(BalanceStatusContext);
  const gameContext = useContext(GameStatusContext);

  const handleCoinSelect = (value: number) => {
    tokenContext.setTokenStatus({
      ...tokenContext.tokenStatus,
      currentToken: value,
    });
  };

  const handleUndo = () => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    )
      if (tokenContext.tokenStatus.selectedToken.length > 0) {
        const selectedToken = tokenContext.tokenStatus.selectedToken;
        const lastSelectedToken = selectedToken.pop();
        tokenContext.setTokenStatus({
          ...tokenContext.tokenStatus,
          selectedToken: selectedToken,
        });

        balanceContext.setBalanceStatus((prev: any) => {
          let amount = 0;
          lastSelectedToken.map((token: any) => {
            amount += token.amount;
          });
          return {
            totalBalance: prev.totalBalance + amount,
            betBalance: prev.betBalance - amount,
          };
        });
      }
  };

  const handleDouble = () => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    )
      if (
        balanceContext.balanceStatus.totalBalance >
        balanceContext.balanceStatus.betBalance
      ) {
        let newTokenList: any[] = [];
        tokenContext.tokenStatus.selectedToken.forEach((tokenList: any) => {
          tokenList.forEach((token: any) => {
            newTokenList.push(token);
          });
        });
        const newSelectedToken: Array<any> =
          tokenContext.tokenStatus.selectedToken;
        newSelectedToken.push(newTokenList);
        tokenContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));

        balanceContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance: prev.totalBalance - prev.betBalance,
          betBalance: prev.betBalance + prev.betBalance,
        }));
      }
  };

  return (
    <div className="flex gap-4 justify-center h-[51px] py-1">

      <h1 className="text-[#958963] text-sm my-auto ">UNDO</h1>
      <button onClick={handleUndo}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="51"
          height="51"
          viewBox="0 0 52 52"
          className="hover:text-[#E9C84D] text-[#7E7243] transition-colors duration-300"
        >
          <g
            id="Ellipse_518"
            data-name="Ellipse 518"
            fill="none"
            stroke="currentColor"
            strokeWidth="1"
          >
            <circle cx="25.5" cy="25.5" r="25.5" stroke="none" />
            <circle cx="26" cy="26" r="25" fill="none" />
          </g>
          <g
            id="_x32_8_1_"
            transform="matrix(-1, -0.017, 0.017, -1, 34.534, 35.211)"
          >
            <g id="icon_13_" transform="translate(0)">
              <g id="Group_540" data-name="Group 540">
                <path
                  id="Path_2250"
                  data-name="Path 2250"
                  d="M12.7,0V.933A6.178,6.178,0,0,0,6.456,7.04,6.178,6.178,0,0,0,12.7,13.147h9.287l-4.3-4.212.694-.677,5.523,5.4h0L18.35,19.1l-.694-.677L22.1,14.08H12.7A7.127,7.127,0,0,1,5.5,7.04,7.127,7.127,0,0,1,12.7,0Z"
                  transform="translate(-5.5 0)"
                  fill="currentColor"
                  stroke="currentColor"
                  strokeWidth="1"
                />
              </g>
            </g>
          </g>
        </svg>
      </button>
      <div className="flex ">
        <div className="flex gap-3 w-fit ">
          {coins.map((coin, index) => (
            <button
              key={index}
              onClick={() => handleCoinSelect(coin.amount)}
              className={`relative hover:transform hover:-translate-y-2 transition duration-300 ${
                coin.amount === tokenContext.tokenStatus.currentToken
                  ? "transform -translate-y-2 rounded-full"
                  : ""
              }`}
            >
              {coin.image}
            </button>
          ))}
        </div>
      </div>
      <button onClick={handleDouble}>
        
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="51"
          height="51"
          viewBox="0 0 52 52"
          className="hover:text-[#E9C84D] text-[#7E7243] transition-colors duration-300"
        >
          <g
            id="Ellipse_518"
            data-name="Ellipse 518"
            fill="none"
            stroke="currentColor"
            strokeWidth="1"
          >
            <circle cx="25.5" cy="25.5" r="25.5" stroke="none" />
            <circle cx="26" cy="26" r="25" fill="none" />
          </g>
          <text
            id="x2"
            transform="translate(15 33)"
            fill="currentColor"
            fontSize="16"
            fontFamily="Montserrat-SemiBold, Montserrat"
            fontWeight="600"
          >
            <tspan x="0" y="0">
              x
            </tspan>
            <tspan y="0" fontSize="21">
              2
            </tspan>
          </text>
        </svg>
      </button>
      <h1 className="text-[#958963] text-sm my-auto ">DOUBLE</h1>
    </div>
  );
};

export default CoinSelect;
