import React, { useState, useContext } from "react";
import GameStatusContext from "../../../../../../context/GameStatusContext";
import TokenStatusContext from "../../../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../../../../context/GlobalActiveItemsContext";
import socketEventsCode from "../../../../../../assets/data/SocketEventCode";
import { NormalCell } from "../../../../../../assets/data/CellData";

import { ReactComponent as SpecialIcon } from "../../../../../../assets/img/favourite-special.svg";

const Special: React.FC = () => {
  const [betNumber] = useState(Array.from({ length: 12 }, (_, i) => i + 1));
  const [PleinNumbers] = useState(Array.from({ length: 10 }, (_, i) => i + 1));

  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);

  const hoverNumbers = (value: number, type: string) => {
    let numList: Array<number> = [];
    if (type === "plein") {
      if (value < 7) numList = [value, value + 10, value + 20, value + 30];
      else numList = [value, value + 10, value + 20];
    } else if (type === "cheval") {
      if (value < 4)
        numList = [
          value,
          value + 3,
          value + 10,
          value + 13,
          value + 20,
          value + 23,
          value + 30,
          value + 33,
        ];
      else
        numList = [
          value,
          value + 3,
          value + 10,
          value + 13,
          value + 20,
          value + 23,
        ];
    } else {
      NormalCell.forEach((cellData) => {
        const tokenButtons = cellData.tokenButtons;
        tokenButtons.forEach((data) => {
          if (data.value.indexOf(value) !== -1) {
            data.value.forEach((num) => {
              if (numList.indexOf(num) === -1) numList.push(num);
            });
          }
        });
      });
    }
    return numList;
  };

  const selectedNumberes = (value: number, type: string) => {
    let numList: Array<any> = [];
    let amount = 0;
    if (type === "plein") {
      if (value < 7) numList = [value, value + 10, value + 20, value + 30];
      else numList = [value, value + 10, value + 20];
      amount = tokenStatusContext.tokenStatus.currentToken * numList.length;
    } else if (type === "cheval") {
      if (value < 4)
        numList = [
          value,
          value + 3,
          value + 10,
          value + 13,
          value + 20,
          value + 23,
          value + 30,
          value + 33,
        ];
      else
        numList = [
          value,
          value + 3,
          value + 10,
          value + 13,
          value + 20,
          value + 23,
        ];
      amount = tokenStatusContext.tokenStatus.currentToken * numList.length;
    } else {
      NormalCell.forEach((cellData) => {
        const tokenButtons = cellData.tokenButtons;
        tokenButtons.forEach((data) => {
          if (data.value.indexOf(value) !== -1) {
            numList.push(data.value);
            amount += tokenStatusContext.tokenStatus.currentToken;
          }
        });
      });
    }
    return { numList, amount };
  };

  const handleMouseEnterEvent = (value: number, type: string) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
    globalActiveItemsContext.setGlobalActiveItems(hoverNumbers(value, type));
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (value: number, type: string) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
    const { numList, amount } = selectedNumberes(value, type);
    if (Number(balanceStatusContext.balanceStatus.totalBalance) >= amount) {
      const newSelectedToken: Array<any> =
        tokenStatusContext.tokenStatus.selectedToken;
      if (type !== "complete") {
        const newBetData: Array<any> = [];
        numList.forEach((num) => {
          newBetData.push({
            value: [num],
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          });
        });
        newSelectedToken.push(newBetData);
      } else {
        const newBetData: Array<any> = [];
        numList.forEach((num) => {
          newBetData.push({
            value: num,
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          });
        });
        newSelectedToken.push(newBetData);
      }
      tokenStatusContext.setTokenStatus((prev: any) => ({
        ...prev,
        selectedToken: newSelectedToken,
      }));
      balanceStatusContext.setBalanceStatus((prev: any) => ({
        ...prev,
        totalBalance: prev.totalBalance - amount,
        betBalance: prev.betBalance + amount,
      }));
    }
    }
  };

  return (
    <div className="p-6 text-center flex flex-col gap-6 w-full">
      <div>
        <h1 className="font-bold text-[#9E7512] border-b border-[#DFAE51]/30 py-1 flex justify-center items-center gap-2">
          <SpecialIcon className="w-6" />
          Finale En Plein
        </h1>
        <div className="w-full flex pt-4 px-6 text-md">
          {PleinNumbers.map((number) => (
            <div
              key={number}
              onMouseEnter={() => handleMouseEnterEvent(number - 1, "plein")}
              onMouseLeave={() => handleMouseLeaveEvent()}
              onClick={() => handleTokenSelect(number - 1, "plein")}
              className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
            >
              {number - 1}
            </div>
          ))}
        </div>
      </div>
      <div>
        <h1 className="font-bold text-[#9E7512] border-b border-[#DFAE51]/30 py-1 flex justify-center items-center gap-2">
          <SpecialIcon className="w-6" />
          Finale A Cheval
        </h1>
        <div className="w-full grid grid-cols-5 pt-4 px-6 text-md">
          {PleinNumbers.map((number) => (
            <div
              onMouseEnter={() => handleMouseEnterEvent(number - 1, "cheval")}
              onMouseLeave={() => handleMouseLeaveEvent()}
              onClick={() => handleTokenSelect(number - 1, "cheval")}
              key={number}
              className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
            >
              {number - 1 + "/" + (number + 2)}
            </div>
          ))}
        </div>
      </div>
      <div>
        <h1 className="font-bold text-[#9E7512] border-b border-[#DFAE51]/30 py-1 flex justify-center items-center gap-2">
          <SpecialIcon className="w-6" />
          Complete Bets
        </h1>
        <div className="grid grid-cols-13 pt-4 px-6 text-md">
          <div className="col-span-1">
            <div
              onMouseEnter={() => handleMouseEnterEvent(0, "complete")}
              onMouseLeave={() => handleMouseLeaveEvent()}
              onClick={() => handleTokenSelect(0, "complete")}
              className="border border-[#7E622D] text-[#907429] w-full h-full flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
            >
              0
            </div>
          </div>
          <div className="col-span-12 grid grid-cols-12">
            {betNumber.map((number, index) => (
              <div
                key={index}
                onMouseEnter={() =>
                  handleMouseEnterEvent(number * 3, "complete")
                }
                onMouseLeave={() => handleMouseLeaveEvent()}
                onClick={() => handleTokenSelect(number * 3, "complete")}
                className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
              >
                {number * 3}
              </div>
            ))}
            {betNumber.map((number, index) => (
              <div
                key={index}
                onMouseEnter={() =>
                  handleMouseEnterEvent(number * 3 - 1, "complete")
                }
                onMouseLeave={() => handleMouseLeaveEvent()}
                onClick={() => handleTokenSelect(number * 3 - 1, "complete")}
                className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
              >
                {number * 3 - 1}
              </div>
            ))}
            {betNumber.map((number, index) => (
              <div
                key={index}
                onMouseEnter={() =>
                  handleMouseEnterEvent(number * 3 - 2, "complete")
                }
                onMouseLeave={() => handleMouseLeaveEvent()}
                onClick={() => handleTokenSelect(number * 3 - 2, "complete")}
                className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
              >
                {number * 3 - 2}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Special;
