import React, {useContext} from "react";
import BalanceStatusContext from "../../../context/BalanceStatusContext";
export const PortraitBalance: React.FC = () => {
  const balanceContext = useContext(BalanceStatusContext);
  return <div className="w-full absolute bottom-0 bg-black h-5 flex justify-between px-4 z-0">
      <div className="absolute right-0 bottom-0 h-[60px] w-screen bg-gradient-to-t from-[#000000] to-[#524500]/10 z-0"></div>
    <div className="flex gap-4 relative z-0">
      <div className="flex justify-center">
        <h1 className="text-white text-xs">BALANCE</h1>
        <div className="absolute top-[-24px] text-lg text-[#EABB25] w-[80px] text-center">{balanceContext.balanceStatus.totalBalance} €</div>
      </div>
      <div className="flex justify-center">
        <h1 className="text-white text-xs">TOTAL BET</h1>
        <div className="absolute top-[-24px] text-lg text-[#EABB25]">{balanceContext.balanceStatus.betBalance} €</div>
      </div>
    </div>
    <div className="flex gap-4 items-center z-0">
      <div className="text-xs text-[#FFB400]">Play Hotties</div>
      <div className="text-xs text-[#9A8546]">€ 0.25-10,000</div>
    </div>
  </div>
};
