import React, { useState, useEffect, useContext } from "react";
import socketEventsCode from "../../../assets/data/SocketEventCode";
import TokenStatusContext from "../../../context/TokenStatusContext";

import BalanceStatusContext from "../../../context/BalanceStatusContext";
import GameStatusContext from "../../../context/GameStatusContext";
import AuthContext from "../../../context/AuthContext";
import { NumCellUI, RowCellUI } from "../../../assets/data/CellUIData";
import ZeroArea from "./GameTable/Table/ZeroArea";
import NumArea from "./GameTable/Table/NumArea";
import RowCellArea from "./GameTable/Table/RowCellArea";
import ColCellArea from "./GameTable/Table/ColCellArea";
import HalfCellArea from "./GameTable/Table/HalfCellArea";
import {
  ColGroupCell,
  ColorCell,
  EvenOddCell,
  HighLowCell,
  NormalCell,
  RowGroupCell,
  ZeroCell,
} from "../../../assets/data/CellData";
import NumAreaMask from "./GameTable/Table/Mask/NumAreaMask";
import OtherAreaMask from "./GameTable/Table/Mask/OtherAreaMask";
import ZeroAreaMask from "./GameTable/Table/Mask/ZeroAreaMask";

import GlobalActiveItemsContext from "../../../context/GlobalActiveItemsContext";
import StatusBar from "./StatusBar";

const GameBoard: React.FC<any> = ({ lastMessage, sendMessage }) => {
  const statusContext = useContext(TokenStatusContext);
  const balanceContext = useContext(BalanceStatusContext);
  const gameContext = useContext(GameStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);
  const authContext = useContext(AuthContext);
  const [activeButtonList, setActiveButtonList] = useState<Array<number>>([]);
  const [winNumber, setWinNumber] = useState<number>(0);

  useEffect(() => {
    if (lastMessage !== null) {
      const responseData = JSON.parse(lastMessage.data);
      responseData.status && gameContext.setGameStatus(responseData.status);
      if (responseData.status === socketEventsCode.SOCKET_BET_CLOSED) {
        const bet_data = statusContext.tokenStatus.selectedToken;
        if (bet_data.length > 0) {
          sendMessage(
            JSON.stringify({
              status_code: "10",
              bet_data: bet_data,
              user_id: authContext.userId,
            })
          );
        }
      }
      if (responseData.status === socketEventsCode.SOCKET_GAME_FINISHED) {
        statusContext.setTokenStatus({ selectedToken: [], currentToken: 1 });
        setWinNumber(responseData.winNumber);
        responseData?.result?.forEach((result: any) => {
          if (result.user === authContext.userId)
            balanceContext.setBalanceStatus((prev: any) => ({
              totalBalance: prev.totalBalance + result.result,
              betBalance: 0,
            }));
        });
        balanceContext.setBalanceStatus((prev: any) => ({
          totalBalance: prev.totalBalance,
          betBalance: 0,
        }));
      }
    }
  }, [lastMessage]);

  useEffect(() => {
    setActiveButtonList(globalActiveItemsContext.globalActiveItems);
  }, [globalActiveItemsContext.globalActiveItems]);

  return (
    <div
      className={`col-span-6 relative rotate-parent ${
        gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
        gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
          ? "pt-[0px] duration-300"
          : "pt-[0px] duration-300"
      }`}
    >
      <StatusBar winNumber={winNumber} />
      <div
        className={`h-fit w-full  border-gray-400 text-lg ${
          gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
          gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
            ? "origin-div"
            : "rotated-div"
        }`}
      >
        <div className="w-full">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="w-full"
            viewBox="0 0 860.404 274.87"
            imageRendering="crisp-edges"
          >
            <ZeroArea activeButtonList={activeButtonList} />
            <g id="Right" transform="translate(-495.97 -712.13)">
              {RowCellUI.map((uiData) => (
                <RowCellArea key={uiData.id} {...uiData} />
              ))}
            </g>

            <g id="Bottom" transform="translate(-495.97 -712.13)">
              <ColCellArea />
              <HalfCellArea />
            </g>
            <g id="Mainboard" transform="translate(1701.404 -9698.13)">
              {NumCellUI.map((uiData) => (
                <NumArea
                  key={uiData.title}
                  {...uiData}
                  activeButtonList={activeButtonList}
                />
              ))}
            </g>
          </svg>
        </div>
        <div className="absolute w-full top-[-20px] left-0">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="w-full"
            viewBox="0 0 860.404 295"
          >
            <g id="Mask" transform="translate(-495.97 -696)">
              <g id="Mainboard" transform="translate(554.875 696)">
                {NormalCell.map((cellData, index) => (
                  <g key={index}>
                    {cellData.tokenButtons.map((token) => (
                      <NumAreaMask
                        key={token.id}
                        {...token}
                        setActiveButtons={setActiveButtonList}
                      />
                    ))}
                  </g>
                ))}
              </g>
              <ZeroAreaMask
                key={ZeroCell.tokenButtons[0].id}
                {...ZeroCell.tokenButtons[0]}
              />
              {ColGroupCell.map((cellData) => (
                <OtherAreaMask
                  key={cellData.tokenButtons[0].id}
                  {...cellData.tokenButtons[0]}
                />
              ))}
              {RowGroupCell.map((cellData) => (
                <OtherAreaMask
                  key={cellData.tokenButtons[0].id}
                  {...cellData.tokenButtons[0]}
                />
              ))}
              {HighLowCell.map((cellData) => (
                <OtherAreaMask
                  key={cellData.tokenButtons[0].id}
                  {...cellData.tokenButtons[0]}
                />
              ))}
              {EvenOddCell.map((cellData) => (
                <OtherAreaMask
                  key={cellData.tokenButtons[0].id}
                  {...cellData.tokenButtons[0]}
                />
              ))}
              {ColorCell.map((cellData) => (
                <OtherAreaMask
                  key={cellData.tokenButtons[0].id}
                  {...cellData.tokenButtons[0]}
                />
              ))}
            </g>
          </svg>
        </div>
      </div>
    </div>
  );
};

export default GameBoard;
