import React, { useState } from "react";
import { ReactComponent as VideoIcon } from "../../../assets/img/videoIcon.svg";

const Video: React.FC = () => {
  const [isVideoShow, setIsVideoShow] = useState<boolean>(true);
  const [selectedQuality, setSelectedQuality] = useState<string>("Auto");
  const qualityOptions = ["High", "Medium", "Low", "Auto"];

  const toggleVideoShow = () => {
    setIsVideoShow((prev) => !prev);
  };

  return (
    <div className="flex flex-col ">
      <h1 className="flex gap-2 items-center text-[#ddb652] text-sm font-bold">
        <VideoIcon className="fill-current w-4" />
        VIDEO
      </h1>
      <div className="flex mx-2 px-4 py-4 justify-between items-center border-b border-[#9E7512]">
        <div className="flex flex-col text-md">
          <h1 className="text-[#9E7512] text-sm font-bold">Video Stream</h1>
          <p className="text-[10px] text-[#946E11]">Turn on / off video</p>
        </div>
        <div
          className="relative flex items-center cursor-pointer"
          onClick={toggleVideoShow}
        >
          <input
            type="checkbox"
            className="sr-only peer"
            checked={isVideoShow}
            onChange={() => setIsVideoShow((prev) => !prev)}
          />
          <div className="flex w-12 h-6 rounded-full peer peer-focus:ring-4 peer-focus:ring-blue-300  bg-[#A88634]/20 peer-checked:after:translate-x-full rtl:peer-checked:after:-translate-x-full  after:content-['off'] peer-checked:after:content-['on'] after:absolute after:top-0 after:start-[2px] after:bg-[#A88634] after:border-[#A88634] after:border after:rounded-full after:h-6 after:w-6 after:transition-all border-gray-600 after:pt-1 peer-checked:after:pt-1 peer-checked:bg-[#A88634]/50 text-[#2A2005]/90 text-xs font-extrabold text-center items-center"></div>
        </div>
      </div>
      <div className="flex flex-col gap-4 mx-2 px-4 py-4 border-b border-[#9E7512]">
        <div className="flex flex-col">
          <h1 className="text-[#9E7512] text-sm font-extrabold">Video Quality</h1>
          <p className="text-[10px] text-[#946E11]">
            Change video quality. Auto is recommended.
          </p>
        </div>
        <div className="flex items-center w-full justify-between gap-3">
          {qualityOptions.map((quality: string, index: number) => (
            <button
              key={index}
              className={`w-full rounded-md text-xs px-2 py-1 text-black ${
                selectedQuality === quality
                  ? "bg-[#DDAE51]"
                  : "bg-[#614D1A]/50 hover:bg-[#614D1A]"
              }`}
              onClick={() => setSelectedQuality(quality)}
            >
              {quality}
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Video;
