import React from "react";
import { Link } from "react-router-dom";

import { ReactComponent as RoomPlay } from "../../assets/img/room-play.svg";
import { ReactComponent as PlayerMan } from "../../assets/img/player-man.svg";

export interface IRoomProps {
  id: number;
  roomName: string;
  liveUser: number;
  betLimit: string;
  roomImage: string;
}

export const Room: React.FC<IRoomProps> = ({
  id,
  roomName,
  liveUser,
  betLimit,
  roomImage,
}) => {
  return (
    <div className="w-full bg-gradient-to-b from-[#CDA026]/60 to-[#45340D]/60 rounded-lg overflow-hidden h-fit relative">
      <div className="h-fit relative pt-1">
        <img
          src={roomImage}
          alt="roomImage"
          loading="lazy"
          className="w-full"
        />
        <div className="absolute top-0 left-0 w-full h-full flex justify-center items-center">
          <Link
            to="/game"
            className="w-[64px] text-[#ad9354] hover:text-[#ffd162] transition-all duration-300"
          >
            <RoomPlay className="w-full" />
          </Link>
        </div>
      </div>
      <div className="flex justify-between items-center px-2 py-1 text-[#82671b] text-xs">
        <h2 className="text-[#FFB400] font-bold">Play {roomName}</h2>
        <div className="flex gap-2">
          <p className="flex gap-1 text-[#82671B]">
            <PlayerMan className="w-3" />
            {liveUser}
          </p>
          <p className="text-[#FFDB68]">{betLimit}</p>
        </div>
      </div>
    </div>
  );
};
