import { createContext } from "react";

export type ITokenStatusContext = {
  tokenStatus: { selectedToken: Array<any>; currentToken: number };
  setTokenStatus: (value: any) => void;
}

const TokenStatusContext = createContext<ITokenStatusContext>({
  tokenStatus: { selectedToken: [], currentToken: 1 },
  setTokenStatus: () => {},
});

export default TokenStatusContext;
