import React, { useContext, useEffect, useState } from "react";
import ReactSlider from "react-slider";

import { ReactComponent as ModalCloseIcon } from "../../../../../assets/img/modal-close.svg";
import { ReactComponent as StatisticsIcon } from "../../../../../assets/img/statistics.svg";
import { ReactComponent as CircleMinus } from "../../../../../assets/img/circle-minus.svg";
import { ReactComponent as CirclePlus } from "../../../../../assets/img/circle-plus.svg";

import Advanced from "./Advanced/index";
import HotCold from "./HotCold/index";
import LastHistory from "./LastHistory";
import { RecentHistory } from "../../../../../components/Game/GameHistory";
import GameHistoryContext from "../../../../../context/HistoryContext";

interface IStatisticsModalProps {
  isShowModal: boolean;
  handleClose: () => void;
}

const Statistics: React.FC<IStatisticsModalProps> = ({
  isShowModal,
  handleClose,
}) => {
  const gameHistoryContext = useContext(GameHistoryContext);

  const [selectedTab, setSelectedTab] = useState<number>(0);
  const [selectedRange, setSelectedRange] = useState<number>(500);
  const [selectedHistory, setSelectedHistory] = useState<Array<any>>([]);

  const tabNames = ["HOT/COLD", "ADVANCED", "LAST 500"];

  const handleRangeChange = (event: any) => {
    setSelectedRange(Number(event.target.value));
  };

  const handleRangeValueChange = (value: number) => {
    setSelectedRange((prev: number) => {
      if (
        prev + value < 50 ||
        prev + value >
          (gameHistoryContext?.gameHistory.length > 500
            ? 500
            : gameHistoryContext.gameHistory.length)
      )
        return prev;
      else return prev + value;
    });
  };

  useEffect(() => {
    setSelectedHistory(gameHistoryContext.gameHistory.slice(0, selectedRange));
  }, [selectedRange, gameHistoryContext.gameHistory]);

  const tabContent = [
    <HotCold selectedHistory={selectedHistory} />,
    <Advanced selectedHistory={selectedHistory} />,
    <LastHistory selectedHistory={selectedHistory} />,
  ];
  return (
    <div
      className={`absolute right-0 bottom-0 z-50 w-full settings-container duration-300 ${
        isShowModal ? "-translate-y-[0px] " : "translate-y-[700px]"
      }`}
    >
      <div className="bg-[#997928] flex justify-between px-4 py-1 relative rounded-t-md">
        <div className="flex gap-2 items-center relative">
          <StatisticsIcon className="w-5" />
          <h1 className="text-black text-md font-bold">STATISTICS</h1>          
        </div>
        <button
          onClick={handleClose}
          className="h-full my-auto text-lg text-black/60 hover:text-black"
        >
          <ModalCloseIcon className="w-5 fill-current" />
        </button>
      </div>
      <div className="bg-gradient-to-b from-[#000000]/95 via-30% via-[#462E01]/95 to-[#754C01] h-full">
        <div className="px-2">
          <div className="flex justify-center gap-4 pt-6">
            {tabNames.map((name, index) => (
              <button
                key={index}
                className={` rounded-md py-2 text-[#32230A] w-[120px] font-bold text-[13px] ${
                  selectedTab === index
                    ? "bg-[#DDAE51] text-[#32230A]"
                    : "bg-transparent hover:bg-[#DDAE51]/50 text-[#C79D48] border-[#C79D48] border-2 "
                }`}
                onClick={() => setSelectedTab(index)}
              >
                {name}
              </button>
            ))}
          </div>
          <div className="flex justify-center py-2 w-full">
            <RecentHistory count={12} />
          </div>
          <div className="w-full h-[370px] text-xl text-white">
            {selectedHistory.length > 0 && tabContent[selectedTab]}
          </div>
        </div>
        <div className="px-2 py-2 bg-[#100F0A]/40 mt-2">
          <h1 className="ps-8 text-xs text-[#e0ad5a]">
            Last {selectedHistory.length} Rounds
          </h1>
          <div className="w-full gap-2 flex items-center">
            <button
              onClick={() => handleRangeValueChange(-1)}
              className="text-[#5a4617] hover:text-[#DDAE51]"
            >
              <CircleMinus className="w-6 fill-current" />
            </button>
            <ReactSlider
              className="horizontal-slider w-[350px] h-4 cursor-pointer rounded-full flex items-center"
              min={50}
              max={
                gameHistoryContext?.gameHistory.length > 500
                  ? 500
                  : gameHistoryContext.gameHistory.length
              }
              value={selectedRange}
              onChange={(value) => setSelectedRange(Number(value))}
              step={1}
              thumbActiveClassName="bg-[#DDAE51] h-5"
              thumbClassName="w-8 h-5 pt-[2px] bg-[#DDAE51] rounded-md text-center text-xs font-extrabold text-[#0D0800]"
              trackClassName-0="track-0"
              trackClassName-1="track-1"
              renderThumb={(props, state) => (
                <div {...props}>{state.valueNow}</div>
              )}
            />
            <button
              onClick={() => handleRangeValueChange(1)}
              className="ms-2 text-[#5a4617] hover:text-[#DDAE51]"
            >
              <CirclePlus className="w-6 fill-current" />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Statistics;
