import React, { useState, useEffect, useContext } from "react";
import TokenStatusContext from "../../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../../context/BalanceStatusContext";
import GameStatusContext from "../../../../../context/GameStatusContext";
import GlobalActiveItems from "../../../../../context/GlobalActiveItemsContext";
import socketEventsCode from "../../../../../assets/data/SocketEventCode";

import Coin025 from "./../../../../../assets/img/cointable/coin1.svg";
import Coin05 from "./../../../../../assets/img/cointable/coin2.svg";
import Coin1 from "./../../../../../assets/img/cointable/coin3.svg";
import Coin5 from "./../../../../../assets/img/cointable/coin4.svg";
import Coin25 from "./../../../../../assets/img/cointable/coin5.svg";
import Coin100 from "./../../../../../assets/img/cointable/coin6.svg";
import Coin500 from "./../../../../../assets/img/cointable/coin7.svg";

const imageList: any = {
  image1: Coin025,
  image2: Coin05,
  image4: Coin1,
  image20: Coin5,
  image100: Coin25,
  image400: Coin100,
  image2000: Coin500,
};

interface IZeroAreaMaskProps {
  id: number;
  value: Array<number>;
}
const ZeroAreaMask: React.FC<IZeroAreaMaskProps> = ({ id, value }) => {
  const tokenContext = useContext(TokenStatusContext);
  const balanceContext = useContext(BalanceStatusContext);
  const gameContext = useContext(GameStatusContext);
  const globalItemsContext = useContext(GlobalActiveItems);
  const [selectedToken, setSelectedToken] = useState<Array<any>>([]);
  const [betAmount, setBetAmount] = useState<number>(0);

  useEffect(() => {
    let newBetAmount = 0;
    tokenContext.tokenStatus.selectedToken.forEach((tokenList: any) => {
      tokenList.map((token: any) => {
        if (
          token.value.toString() === value.toString() &&
          token.type === "mainboard"
        )
          newBetAmount += token.amount;
      });
    });
    setBetAmount(newBetAmount);
    const newSelectedToken: Array<any> = tokenContext.tokenStatus.selectedToken;
    setSelectedToken(newSelectedToken);
  }, [tokenContext.tokenStatus]);

  const handleMouseEnterEvent = () => {
    (gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
      globalItemsContext.setGlobalActiveItems(value);
  };

  const handleMouseLeaveEvent = () => {
    globalItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = () => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      if (
        Number(balanceContext.balanceStatus.totalBalance) >=
        Number(tokenContext.tokenStatus.currentToken)
      ) {
        const newSelectedToken: Array<any> =
          tokenContext.tokenStatus.selectedToken;
        newSelectedToken.push([
          {
            id: id,
            value: value,
            amount: tokenContext.tokenStatus.currentToken,
            type: "mainboard",
          },
        ]);
        tokenContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance:
            prev.totalBalance - Number(tokenContext.tokenStatus.currentToken),
          betBalance:
            prev.betBalance + Number(tokenContext.tokenStatus.currentToken),
        }));
      }
    }
  };
  return (
    <>
      <g transform="translate(240 60)">
        {[...selectedToken].reverse().map((tokenList: any) =>
          tokenList.map(
            (token: any, index: number) =>
              token.value.toString() === value.toString() &&
              token.type === "mainboard" && (
                <g key={index}>
                  <image
                    className="h-9"
                    transform={`translate(-18 -18)`}
                    href={imageList[`image${token.amount * 4}`]}
                  />
                  <text
                    dominantBaseline="middle"
                    textAnchor="middle"
                    stroke={`${token.amount * 4 !== 1 ? "#ffffff" : "#107dd0"}`}
                    fontSize="14"
                    fontFamily="Montserrat"
                    fontWeight={800}
                    letterSpacing="-0.05em"
                  >
                    <tspan x="0" y="0">
                      {betAmount}
                    </tspan>
                  </text>
                </g>
              )
          )
        )}
      </g>
      <path
        id="Zero"
        d="M147.327,81.722V61.334s-.52-4.819,3.738-6.061,82.85-19.424,82.85-19.424,3.911-1.151,6.657,0,86.661,21.2,86.661,21.2,4.185.689,4.185,4.287V81.722Z"
        transform="translate(0 -1)"
        fill="#fff"
        opacity={0}
        strokeWidth="1"
        onTouchStart={handleMouseEnterEvent}
        onTouchEnd={handleMouseLeaveEvent}
        onClick={handleTokenSelect}
      />
    </>
  );
};

export default ZeroAreaMask;
