import React from "react";
import { ReactComponent as ModalCloseIcon } from "../../../../../assets/img/modal-close.svg";
import { ReactComponent as MainMenuIcon } from "../../../../../assets/img/settings.svg";
interface IMenuModalProps {
  isShowModal: boolean;
  handleClose: () => void;
}

const MenuModal: React.FC<IMenuModalProps> = ({ isShowModal, handleClose }) => {
  return (
    <div
      className={`absolute right-0 bottom-0 z-50 w-full settings-container duration-300 ${
        isShowModal ? "-translate-y-[0px] " : "translate-y-[700px]"
      }`}
    >
      <div className="bg-[#997928] flex justify-between px-4 py-1 relative rounded-t-md">
        <div className="flex gap-2 items-center relative">
          <MainMenuIcon className="w-5" />
          <h1 className="text-black text-md font-bold">MainMenu</h1>
          <h1 className="absolute right-0 text-black text-md font-bold">
            MainMenu
          </h1>
        </div>
        <button
          onClick={handleClose}
          className="h-full my-auto text-lg text-black/60 hover:text-black"
        >
          <ModalCloseIcon className="w-5 fill-current" />
        </button>
      </div>
      <div className="bg-gradient-to-b from-[#1A1300]/95 to-[#352404]/95 h-full ">
        <div className="grid grid-cols-3">
          <div className="bg-[#F6E7C8]/20 rounded-lg">
            
          </div>
        </div>
      </div>
    </div>
  );
};

export default MenuModal;
