import React, { useContext, useState } from "react";
import clsx from "classnames";
import GameStatusContext from "../../../../context/GameStatusContext";
import PortraitMainboard from "./MainBoard";
import PortraitMainMask from "./MainMask";
import socketEventsCode from "../../../../assets/data/SocketEventCode";
import PortraitRaceTrackBoard from "./RaceTrackBoard";
import PortraitRaceTrackMask from "./RaceTrackMask";

const PortraitMainPanel: React.FC<any> = ({ isMainBoard }) => {
  const gameStatusContext = useContext(GameStatusContext);
  const [raceTrackActiveItems, setRaceTrackActiveItems] = useState([]);
  return (
    <div
      className={clsx(
        "bg-[#000000]/30 w-fit h-full m-4 rounded-lg border-[#C7B77C]/20 border-4 relative",
        gameStatusContext.gameStatus ===
          socketEventsCode.SOCKET_GAME_FINISHED ||
          gameStatusContext.gameStatus ===
            socketEventsCode.SOCKET_PLACE_YOUR_BETS
          ? "origin-scale-div"
          : "scaled-div"
      )}
    >
      {isMainBoard ? (
        <>
          <PortraitMainboard />
          <PortraitMainMask />
        </>
      ) : (
        <>
          <PortraitRaceTrackBoard raceTrackActiveItems={raceTrackActiveItems} />
          <PortraitRaceTrackMask
            raceTrackCount={2}
            setRaceTrackActiveItems={setRaceTrackActiveItems}
          />
        </>
      )}
    </div>
  );
};

export default PortraitMainPanel;
