import React, { useContext } from "react";
import clsx from "classnames";

import socketEventsCode from "../../../../../assets/data/SocketEventCode";
import GameStatusContext from "../../../../../context/GameStatusContext";

const ColCellArea: React.FC = () => {
  const gameStatusContext = useContext(GameStatusContext);
  return (
    <g
      className={clsx(
        "transition-all duration-300",
        gameStatusContext.gameStatus ===
          socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
          gameStatusContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
          ? "opacity-85"
          : "opacity-60"
      )}
    >
      <g>
        <g
          id="Com1st"
          transform="translate(569.374 899)"
          fill="#222"
          stroke="#969b7f"
          strokeWidth="1"
          opacity="0.66"
        >
          <rect width="244" height="42" rx="3" stroke="none" />
          <rect x="0.5" y="0.5" width="243" height="41" rx="2.5" fill="none" />
        </g>
        <text
          id="_1st_12"
          data-name="1st   12"
          transform="translate(669 929)"
          fill="#958963"
          fontSize="20"
          fontFamily="Montserrat"
        >
          <tspan x="0" y="0">
            1
          </tspan>
          <tspan
            y="0"
            xmlSpace="preserve"
            fontSize="11.666"
            baselineShift="6.666000209254424"
          >
            st{" "}
          </tspan>
          <tspan y="0">12</tspan>
        </text>
      </g>

      <g>
        <g
          id="Com2nd"
          transform="translate(817.374 899)"
          fill="#222"
          stroke="#969b7f"
          strokeWidth="1"
          opacity="0.66"
        >
          <rect width="244" height="42" rx="3" stroke="none" />
          <rect x="0.5" y="0.5" width="243" height="41" rx="2.5" fill="none" />
        </g>
        <text
          id="_2nd_12"
          data-name="2nd   12"
          transform="translate(916 929)"
          fill="#958963"
          fontSize="20"
          fontFamily="Montserrat"
        >
          <tspan x="0" y="0">
            2
          </tspan>
          <tspan
            y="0"
            xmlSpace="preserve"
            fontSize="11.666"
            baselineShift="6.666000209254424"
          >
            nd{" "}
          </tspan>
          <tspan y="0">12</tspan>
        </text>
      </g>
      <g>
        <g
          id="Com3rd"
          transform="translate(1065.374 899)"
          fill="#222"
          stroke="#969b7f"
          strokeWidth="1"
          opacity="0.66"
        >
          <rect width="244" height="42" rx="3" stroke="none" />
          <rect x="0.5" y="0.5" width="243" height="41" rx="2.5" fill="none" />
        </g>
        <text
          id="_3rd_12"
          data-name="3rd   12"
          transform="translate(1163 929)"
          fill="#958963"
          fontSize="20"
          fontFamily="Montserrat"
        >
          <tspan x="0" y="0">
            3
          </tspan>
          <tspan
            y="0"
            xmlSpace="preserve"
            fontSize="11.666"
            baselineShift="6.666000209254424"
          >
            rd{" "}
          </tspan>
          <tspan y="0">12</tspan>
        </text>
      </g>
    </g>
  );
};

export default ColCellArea;
