import React, { useState, useEffect, useContext, useRef } from "react";
import AuthContext from "../../../context/AuthContext";
import socketEventsCode from "../../../assets/data/SocketEventCode";
import ChatIcon from "../../../assets/img/chat.svg";

const Chat: React.FC<any> = ({ lastMessage, sendMessage }) => {
  const { userId } = useContext(AuthContext);
  const [chatMsg, setChatMsg] = useState("");
  const [socketMsg, setSocketMsg] = useState<Array<any>>([]);
  const chatHistoryRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    chatHistoryRef.current?.scrollIntoView({behavior: "smooth", block: "end"});
  }, [socketMsg]);

  useEffect(() => {
    if (lastMessage !== null) {
      const responseData = JSON.parse(lastMessage.data);
      if (responseData.event === socketEventsCode.SOCKET_NEW_CHAT) {
        setSocketMsg([
          ...socketMsg,
          {
            userId: responseData.userId,
            message: responseData.message,
          },
        ]);
        chatHistoryRef.current?.scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      }
    }
  }, [lastMessage]);

  const handleMsgChange = (event: any) => {
    setChatMsg(event.target.value);
  };

  const handleKeyDown = (event: any) => {
    if (event.key === "Enter") {
      sendMessage(
        JSON.stringify({
          status_code: "11",
          user_id: userId,
          message: chatMsg,
        })
      );
      setChatMsg("");
    }
  };
  return (
    <div>
      <div className="w-full max-w-[250px] ps-4 relative">
        <div className="absolute top-3 left-8">
          <img src={ChatIcon} alt="chat" />
        </div>
        <input
          value={chatMsg}
          onChange={handleMsgChange}
          onKeyDown={handleKeyDown}
          placeholder="CLICK TO CHAT"
          className="w-full px-10 py-2 rounded-full text-[10px] text-left bg-[#000000A0] text-white border focus:outline-none border-[#FFD962]/40 placeholder-[#FFD162]"
        />
      </div>
      <div
        className="ps-4 py-1 overflow-hidden max-h-[300px] w-fit z-50"
      >
        {socketMsg.length > 0 &&
          socketMsg.map((msg: any, index: number) => (
            <div
              key={index}
              className="flex gap-2 items-center rounded-md bg-transparent leading-4 text-white text-md px-2 my-2 animate-fade"
            >
              <h1 className=" text-xs">{msg.userId}:</h1>
              <p className="text-md font-medium">{msg.message}</p>
            </div>
          ))}
          <div ref={chatHistoryRef} />
      </div>
    </div>
  );
};

export default Chat;
