import React, { useEffect, useState } from "react";
import clsx from "classnames";

import GamePanel from "./GamePanel";
import PortraitLogo from "../../../assets/img/PortraitLogo.png";
import PortraitLogo1 from "../../../assets/img/LIVE_ok.png";
import PortraitLogo2 from "../../../assets/img/Subtraction -1.png";
import ProcessImage from "../../../assets/img/04.png";
const PortraitGame: React.FC = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [process, setProcess] = useState(0);
  useEffect(() => {
    let intervalID: any;
    if (isLoading === true) {
      intervalID = setInterval(() => {
        setProcess((prev: number) => {
          console.log(prev);
          if (prev !== 100) return prev + 20;
          return prev;
        });
      }, 1000);
    }
    return () => {
      clearInterval(intervalID);
    };
  }, [isLoading]);
  useEffect(() => {
    if (isLoading === true)
      setTimeout(() => {
        setIsLoading(false);
      }, 6000);
  }, [isLoading]);
  return (
    <div className="w-screen h-screen relative">
      {isLoading ? (
        <div className="loading w-full h-full">
          <div className="w-full flex justify-center pt-[100px]">
            <img
              className="w-full max-w-[500px]"
              src={PortraitLogo}
              alt="portrait logo"
            />
          </div>
          <div className="w-full flex justify-center">
            <img src={PortraitLogo1} alt="portrait logo1" />
          </div>
          <div className="w-full flex justify-center">
            <div className=" w-full max-w-[250px] flex h-2 rounded-lg overflow-visible relative">
              <div
                style={{
                  width: `${process}%`,
                }}
                className={clsx(
                  "transition-all transform duration-300",
                  `h-2 rounded-l-lg`,
                  "bg-[#D49C3A]"
                )}
              ></div>
              <div
                style={{
                  width: `${100 - process}%`,
                }}
                className={clsx(
                  "transition-all transform duration-300 overflow-visible relative",
                  `h-2 rounded-r-lg`,
                  "bg-[#790909]"
                )}
              >
              </div>
              <img
                style={{
                  left: `${250 * process / 100}px`
                }}
                className="absolute transition-all transform duration-300 w-20 h-20 top-1 -translate-x-1/2 -translate-y-1/2"
                src={ProcessImage}
                alt="process"
              />
            </div>
          </div>
          <div className="w-full absolute bottom-0 flex justify-center">
            <img src={PortraitLogo2} alt="portrait logo1" />
          </div>
        </div>
      ) : (
        <>
          <div className="absolute w-full h-full top-0 left-0 flex flex-col z-0">
            <div className="h-full portrait-background1"></div>
            <div className="portrait-background h-[120vh]"></div>
          </div>
          <GamePanel />
        </>
      )}
    </div>
  );
};

export default PortraitGame;
