import React, { useContext } from "react";
import clsx from "classnames";

import socketEventsCode from "../../../../../assets/data/SocketEventCode";
import GameStatusContext from "../../../../../context/GameStatusContext";
interface IRowCellAreaProps {
  id: string;
  key: string;
  areaTransform: string;
  titleTransform: string;
}

const RowCellArea: React.FC<IRowCellAreaProps> = ({
  areaTransform,
  titleTransform,
}) => {
  const gameStatusContext = useContext(GameStatusContext);
  return (
    <g>
      <g
        transform={areaTransform}
        className={clsx(
          "transition-all duration-300",
          gameStatusContext.gameStatus ===
            socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
            gameStatusContext.gameStatus ===
              socketEventsCode.SOCKET_GAME_FINISHED
            ? "opacity-85"
            : "opacity-60"
        )}
        fill="#2d2d2d"
        stroke="#8d966c"
        strokeWidth="1"
        opacity="0.78"
      >
        <rect width="44" height="58" rx="3" stroke="none" />
        <rect x="0.5" y="0.5" width="43" height="57" rx="2.5" fill="none" />
      </g>
      <text
        transform={titleTransform}
        fill="#958963"
        fontSize="18"
        fontWeight={600}
        fontFamily="Montserrat"
      >
        <tspan x="0" y="0">
          2:1
        </tspan>
      </text>
    </g>
  );
};

export default RowCellArea;
