import React, { useState, useEffect, useContext } from "react";
import GameStatusContext from "../../../context/GameStatusContext";
import socketEventsCode from "../../../assets/data/SocketEventCode";

const TimeCounter:React.FC<any> = ({ lastMessage }) => {
  const [countVal, setCounterVal] = useState<number>(0);
  const gameStatusContext = useContext(GameStatusContext);

  useEffect(() => {
    if (lastMessage !== null) {
      const responseData = JSON.parse(lastMessage.data);
      if(responseData.event === socketEventsCode.SOCKET_BALL_STARTED) {
        setCounterVal(45);
      }
    }
  }, [lastMessage])

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCounterVal((prev: number) => {
        if (prev >= 0.01) return prev - 0.01;
        return prev;
      });
    }, 10);
    return () => {
      clearInterval(intervalId);
    };
  }, []);

  const formatDecimalNumber = (value: number) => {
    const formattedString = value.toFixed(2);
    const [integerPart, decimalPart] = formattedString.split(".");

    const formattedIntegerPart = integerPart.padStart(2, "0");
    const formattedDecimalPart = decimalPart.padEnd(2, "0");

    return `${formattedIntegerPart}.${formattedDecimalPart}`;
  };

  return <div className="bg-black text-white text-[40px] leading-[1] rounded-full w-[180px] ps-9 py-[8px] h-fit me-4">{formatDecimalNumber(countVal)}</div>;
};
export default TimeCounter;
