import React, { useState, useContext } from "react";
import GameStatusContext from "../../../../../../context/GameStatusContext";
import TokenStatusContext from "../../../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../../../../context/GlobalActiveItemsContext";
import socketEventsCode from "../../../../../../assets/data/SocketEventCode";
import * as CellData from "../../../../../../assets/data/CellData";

const StraightUp: React.FC = () => {
  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);
  const handleMouseEnterEvent = (value: Array<number>) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
      globalActiveItemsContext.setGlobalActiveItems(value);
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (value: Array<number>) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      if (
        Number(balanceStatusContext.balanceStatus.totalBalance) >=
        tokenStatusContext.tokenStatus.currentToken * value.length
      ) {
        const newSelectedToken: Array<any> =
          tokenStatusContext.tokenStatus.selectedToken;
        const newBetData: Array<any> = [];
        value.forEach((num) => {
          newBetData.push({
            value: [num],
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          });
        });
        newSelectedToken.push(newBetData);
        tokenStatusContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceStatusContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance:
            prev.totalBalance -
            tokenStatusContext.tokenStatus.currentToken * value.length,
          betBalance:
            prev.betBalance +
            tokenStatusContext.tokenStatus.currentToken * value.length,
        }));
      }
    }
  };
  return (
    <div className="w-full text-center flex flex-col gap-8 justify-center px-12 py-8 text-[16px]">
      <div className="w-full">
        <div className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer">
          STRAIGHT UP
        </div>
        <div className="w-full text-white h-10 flex justify-center items-center">
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(CellData.ColorCell[0].tokenButtons[0].value)
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.ColorCell[0].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            RED
          </div>
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(CellData.ColorCell[1].tokenButtons[0].value)
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.ColorCell[1].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            BLACK
          </div>
        </div>
        <div className="w-full text-white h-10 flex justify-center items-center">
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.EvenOddCell[0].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.EvenOddCell[0].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            ODD
          </div>
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.EvenOddCell[1].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.EvenOddCell[1].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            EVEN
          </div>
        </div>
        <div className="w-full text-white h-10 flex justify-center items-center">
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.HighLowCell[0].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.HighLowCell[0].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            1-18
          </div>
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.HighLowCell[1].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.HighLowCell[1].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            19-36
          </div>
        </div>
        <div className="w-full text-white h-10 flex justify-center items-center">
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.ColGroupCell[0].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.ColGroupCell[0].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            1ST COLUMN
          </div>
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.RowGroupCell[0].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.RowGroupCell[0].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            1ST DOZEN
          </div>
        </div>
        <div className="w-full text-white h-10 flex justify-center items-center">
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.ColGroupCell[1].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.ColGroupCell[1].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            2ND COLUMN
          </div>
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.RowGroupCell[1].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.RowGroupCell[1].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            2ND DOZEN
          </div>
        </div>
        <div className="w-full text-white h-10 flex justify-center items-center">
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.ColGroupCell[2].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.ColGroupCell[2].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            3RD COLUMN
          </div>
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.RowGroupCell[2].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.RowGroupCell[2].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            3RD DOZEN
          </div>
        </div>
      </div>
      <div className="w-full">
        <div className="w-full text-white h-10 flex justify-center items-center">
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.RaceTrackCell[0].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.RaceTrackCell[0].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            TIER
          </div>
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.RaceTrackCell[1].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.RaceTrackCell[1].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            ORPHELINS
          </div>
        </div>
        <div className="w-full text-white h-10 flex justify-center items-center">
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.RaceTrackCell[2].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.RaceTrackCell[2].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            VOISINS
          </div>
          <div
            onMouseEnter={() =>
              handleMouseEnterEvent(
                CellData.RaceTrackCell[3].tokenButtons[0].value
              )
            }
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() =>
              handleTokenSelect(CellData.RaceTrackCell[3].tokenButtons[0].value)
            }
            className="border border-[#7E622D] text-[#907429] w-full h-10 flex justify-center items-center hover:bg-[#503C16] hover:text-white transition-colors duration-300 cursor-pointer"
          >
            ZERO
          </div>
        </div>
      </div>
    </div>
  );
};

export default StraightUp;
