import React, { useState, useEffect, useContext } from "react";
import GameStatusContext from "../../../../context/GameStatusContext";
import TokenStatusContext from "../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../../context/GlobalActiveItemsContext";

import socketEventsCode from "../../../../assets/data/SocketEventCode";

import * as CellData from "../../../../assets/data/CellData";
interface IRowHistoryProps {
  selectedHistory: Array<any>;
}

const RowHistory: React.FC<IRowHistoryProps> = ({ selectedHistory }) => {
  const [rowHistory, setRowHistory] = useState({
    zero: 0,
    first: 0,
    second: 0,
    third: 0,
  });

  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);

  const handleMouseEnterEvent = (value: Array<number>) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
      globalActiveItemsContext.setGlobalActiveItems(value);
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (value: Array<number>) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      if (
        Number(balanceStatusContext.balanceStatus.totalBalance) >=
        Number(tokenStatusContext.tokenStatus.currentToken)
      ) {
        const newSelectedToken: Array<any> =
          tokenStatusContext.tokenStatus.selectedToken;

        newSelectedToken.push([
          {
            value: value,
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          },
        ]);
        tokenStatusContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceStatusContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance:
            prev.totalBalance -
            Number(tokenStatusContext.tokenStatus.currentToken),
          betBalance:
            prev.betBalance +
            Number(tokenStatusContext.tokenStatus.currentToken),
        }));
      }
    }
  };

  const calcRowHistory = () => {
    const newRowHistory = {
      zero: 0,
      first: 0,
      second: 0,
      third: 0,
    };
    selectedHistory.forEach((history) => {
      const { winNumber } = history;
      if (CellData.ZeroCell.tokenButtons[0].value.indexOf(winNumber) !== -1)
        newRowHistory.zero++;
      else if (
        CellData.RowGroupCell[0].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newRowHistory.first++;
      else if (
        CellData.RowGroupCell[1].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newRowHistory.second++;
      else if (
        CellData.RowGroupCell[2].tokenButtons[0].value.indexOf(winNumber) !== -1
      )
        newRowHistory.third++;
    });
    setRowHistory({
      zero: Number(
        ((100 * newRowHistory.zero) / selectedHistory.length).toFixed(0)
      ),
      first: Number(
        ((100 * newRowHistory.first) / selectedHistory.length).toFixed(0)
      ),
      second: Number(
        ((100 * newRowHistory.second) / selectedHistory.length).toFixed(0)
      ),
      third: Number(
        ((100 * newRowHistory.third) / selectedHistory.length).toFixed(0)
      ),
    });
  };
  useEffect(() => {
    calcRowHistory();
  }, [selectedHistory]);
  return (
    <div className="flex px-4 w-full h-[40px] text-sm gap-1">
      <div className="w-[5%]">
        <button
          onMouseEnter={() =>
            handleMouseEnterEvent(CellData.ZeroCell.tokenButtons[0].value)
          }
          onMouseLeave={() => handleMouseLeaveEvent()}
          onClick={() =>
            handleTokenSelect(CellData.ZeroCell.tokenButtons[0].value)
          }
          className="w-full relative transition-colors duration-300 bg-[#00b01c]/60 hover:bg-[#00b01c] h-full border border-transparent flex justify-center items-center"
        >
          <h1>0</h1>
          <p className="absolute top-[-18px] text-[#877348]">{rowHistory.zero}%</p>
        </button>
      </div>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.RowGroupCell[0].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.RowGroupCell[0].tokenButtons[0].value)
        }
        style={{ width: `${rowHistory.first}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/60 hover:bg-[#F6E7C8]/80 h-full border border-transparent flex justify-center items-center w-[${rowHistory.first}%]`}
      >
        <h1>1ST DOZEN</h1>
        <p className="absolute top-[-18px] text-[#877348]">{rowHistory.first}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.RowGroupCell[1].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.RowGroupCell[1].tokenButtons[0].value)
        }
        style={{ width: `${rowHistory.second}%` }}
        className={`relative transition-colors duration-300 text-black/90 bg-[#F6E7C8]/40 hover:bg-[#F6E7C8]/80 h-full border border-transparent flex justify-center items-center w-[${rowHistory.second}%]`}
      >
        <h1>2ND DOZEN</h1>
        <p className="absolute top-[-18px] text-[#877348]">{rowHistory.second}%</p>
      </button>
      <button
        onMouseEnter={() =>
          handleMouseEnterEvent(CellData.RowGroupCell[2].tokenButtons[0].value)
        }
        onMouseLeave={() => handleMouseLeaveEvent()}
        onClick={() =>
          handleTokenSelect(CellData.RowGroupCell[2].tokenButtons[0].value)
        }
        style={{ width: `${rowHistory.third}%` }}
        className={`relative transition-colors duration-300 text-black/80 bg-[#F6E7C8]/60 hover:bg-[#F6E7C8]/80 h-full border border-transparent flex justify-center items-center w-[${rowHistory.third}%]`}
      >
        <h1>3RD DOZEN</h1>
        <p className="absolute top-[-18px] text-[#877348]">{rowHistory.third}%</p>
      </button>
    </div>
  );
};

export default RowHistory;
