import React, { useState, useEffect, useContext } from "react";
import classNames from "classnames";

import { ReactComponent as HotIcon } from "../../../../assets/img/hot-statistics.svg";
import { ReactComponent as ColdIcon } from "../../../../assets/img/cold-statistics.svg";

import { CircularBarplot } from "./CircularBarplot";
import GameStatusContext from "../../../../context/GameStatusContext";
import TokenStatusContext from "../../../../context/TokenStatusContext";
import BalanceStatusContext from "../../../../context/BalanceStatusContext";
import GlobalActiveItemsContext from "../../../../context/GlobalActiveItemsContext";
import socketEventsCode from "../../../../assets/data/SocketEventCode";

interface IHotColdProps {
  selectedHistory: Array<any>;
}
const redData = [
  1, 3, 5, 7, 9, 12, 14, 16, 18, 19, 21, 23, 25, 27, 30, 32, 34, 36,
];
const blackData = [
  2, 4, 6, 8, 10, 11, 13, 15, 17, 20, 22, 24, 26, 28, 29, 31, 33, 35,
];
const betNumberList = [
  20, 14, 31, 9, 22, 18, 29, 7, 28, 12, 35, 3, 26, 0, 32, 15, 19, 4, 21, 2, 25,
  17, 34, 6, 27, 13, 36, 11, 30, 8, 23, 10, 5, 24, 16, 33, 1,
];
const HotCold: React.FC<IHotColdProps> = ({ selectedHistory }) => {
  const [sortedBetHistory, setSortedBetHistory] = useState<Array<any>>([]);
  const [betHistory, setBetHistory] = useState<Array<any>>([]);
  const gameStausContext = useContext(GameStatusContext);
  const balanceStatusContext = useContext(BalanceStatusContext);
  const tokenStatusContext = useContext(TokenStatusContext);
  const globalActiveItemsContext = useContext(GlobalActiveItemsContext);
  const [maxCount, setMaxCount] = useState<number>(0);

  useEffect(() => {
    let calcBet = Array.from({ length: 37 }, () => 0);
    let max = 0;
    selectedHistory.forEach((history) => {
      calcBet[history.winNumber]++;
      if (max < calcBet[history.winNumber]) max = calcBet[history.winNumber];
    });
    let newBetHistory: Array<any> = [];
    let newSortHistory: Array<any> = [];
    betNumberList.forEach((number) => {
      newBetHistory.push({ name: String(number), value: calcBet[number] });
      newSortHistory.push({ name: String(number), value: calcBet[number] });
    });
    setBetHistory(newBetHistory);
    newSortHistory.sort(
      (historyA: any, historyB: any) => historyB.value - historyA.value
    );
    setMaxCount(max);
    setSortedBetHistory(newSortHistory);
  }, [selectedHistory]);

  const getClassAttribute = (winNumber: number) => {
    let className = "";
    if (redData.indexOf(winNumber) !== -1)
      className = "text-red-600 bg-transparent ";
    else if (blackData.indexOf(winNumber) !== -1)
      className = "text-white bg-transparent ";
    else className = "text-green-600 bg-transparent ";
    return className;
  };

  const handleMouseEnterEvent = (value: number) => {
    (gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED) &&
      globalActiveItemsContext.setGlobalActiveItems([value]);
  };

  const handleMouseLeaveEvent = () => {
    globalActiveItemsContext.setGlobalActiveItems([]);
  };

  const handleTokenSelect = (value: number) => {
    if (
      gameStausContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameStausContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    ) {
      if (
        Number(balanceStatusContext.balanceStatus.totalBalance) >=
        Number(tokenStatusContext.tokenStatus.currentToken)
      ) {
        const newSelectedToken: Array<any> =
          tokenStatusContext.tokenStatus.selectedToken;

        newSelectedToken.push([
          {
            value: [value],
            amount: tokenStatusContext.tokenStatus.currentToken,
            type: "mainboard",
          },
        ]);
        tokenStatusContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));
        balanceStatusContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance:
            prev.totalBalance -
            Number(tokenStatusContext.tokenStatus.currentToken),
          betBalance:
            prev.betBalance +
            Number(tokenStatusContext.tokenStatus.currentToken),
        }));
      }
    }
  };

  return (
    <div className="grid grid-cols-8 w-full px-6 pt-6">
      <div className="col-span-1 flex flex-col items-center border border-[#C80300]/50 p-1 h-fit">
        <div className="w-full h-12 flex justify-center items-center bg-[#BC1108]/40">
          <HotIcon className="w-6" />
        </div>
        {sortedBetHistory.slice(0, 5).map((history: any, index) => (
          <button
            className={classNames(
              "relative w-12 h-12 my-1 flex justify-center items-center rounded-md bg-[#7E631C]/20 hover:bg-[#7C6820]/50 transition-colors duration-300",
              getClassAttribute(Number(history.name))
            )}
            onMouseEnter={() => handleMouseEnterEvent(Number(history.name))}
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() => handleTokenSelect(Number(history.name))}
            key={index}
          >
            {history.name}
          </button>
        ))}
      </div>
      <div className="col-span-6">
        <CircularBarplot
          data={betHistory}
          width={335}
          height={335}
          sortedBetHistory={sortedBetHistory}
          maxCount={maxCount}
        />
      </div>
      <div className="col-span-1 flex flex-col border border-[#0098FD]/60 p-1 items-center h-fit">
        <div className="w-full h-12 flex justify-center items-center bg-[#0098FD]/30">
          <ColdIcon className="w-6" />
        </div>
        {sortedBetHistory.slice(32, 37).map((history: any, index) => (
          <button
            className={classNames(
              "relative w-12 h-12 my-1 flex justify-center items-center rounded-md bg-[#7E631C]/20 hover:bg-[#7C6820]/50 transition-colors duration-300",
              getClassAttribute(Number(history.name))
            )}
            onMouseEnter={() => handleMouseEnterEvent(Number(history.name))}
            onMouseLeave={() => handleMouseLeaveEvent()}
            onClick={() => handleTokenSelect(Number(history.name))}
            key={index}
          >
            {history.name}
          </button>
        ))}
      </div>
    </div>
  );
};

export default HotCold;
