import React, { useState, useContext } from "react";
import clsx from "classnames";

import { ReactComponent as PortraitStatistics } from "../../../assets/img/portrait-statistics.svg";
import { ReactComponent as PortraitFavourite } from "../../../assets/img/portrait-favourite.svg";
import { ReactComponent as PortraitRaceTrackSwitch } from "../../../assets/img/racetrack-switch.svg";
import { ReactComponent as PortraitAutoPlay } from "../../../assets/img/auto-play.svg";
import { ReactComponent as PortraitMenuIcon } from "../../../assets/img/menu-icon.svg";
import { ReactComponent as GameUndo } from "../../../assets/img/undo.svg";
import { ReactComponent as GameDouble } from "../../../assets/img/double.svg";
import PortraitCoinSelect from "./CoinsSelect";
import TokenStatusContext from "../../../context/TokenStatusContext";
import GameStatusContext from "../../../context/GameStatusContext";
import BalanceStatusContext from "../../../context/BalanceStatusContext";
import socketEventsCode from "../../../assets/data/SocketEventCode";
import Statistics from "../../../layouts/game/PortraitGame/Modal/Statistics";
import FavouiteModal from "../../../layouts/game/PortraitGame/Modal/FavouriteModal";
import MenuModal from "../../../layouts/game/PortraitGame/Modal/MenuModal";
import HottiesLogo from "../../../assets/img/Hotties_Logo_MOBILE.png";

export const PortraitMenu: React.FC<any> = ({ setIsMainBoard }) => {
  const gameContext = useContext(GameStatusContext);
  const tokenContext = useContext(TokenStatusContext);
  const balanceContext = useContext(BalanceStatusContext);
  const [isStatisticsModalOpen, setIsStatisticsModalOpen] =
    useState<boolean>(false);
  const [isFavouriteOpen, setIsFavouriteOpen] = useState<boolean>(false);
  const [isMenuModalOpen, setIsMenuModalOpen] = useState<boolean>(false);
  const handleUndo = () => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    )
      if (tokenContext.tokenStatus.selectedToken.length > 0) {
        const selectedToken = tokenContext.tokenStatus.selectedToken;
        const lastSelectedToken = selectedToken.pop();
        tokenContext.setTokenStatus({
          ...tokenContext.tokenStatus,
          selectedToken: selectedToken,
        });

        balanceContext.setBalanceStatus((prev: any) => {
          let amount = 0;
          lastSelectedToken.map((token: any) => {
            amount += token.amount;
          });
          return {
            totalBalance: prev.totalBalance + amount,
            betBalance: prev.betBalance - amount,
          };
        });
      }
  };

  const handleCloseStatisticsModal = () => {
    setIsStatisticsModalOpen(false);
  };
  const handleOpenStatisticsModal = () => {
    setIsStatisticsModalOpen(true);
    setIsFavouriteOpen(false);
  };
  const hanldeCloseFavouriteModal = () => {
    setIsFavouriteOpen(false);
  };
  const handleOpenFavouriteModal = () => {
    setIsFavouriteOpen(true);
    setIsStatisticsModalOpen(false);
  };
  const handleOpenMenuModal = () => {
    setIsMenuModalOpen(true);
  };
  const handleCloseMenuModal = () => {
    setIsMenuModalOpen(false);
  };
  const handleDouble = () => {
    if (
      gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS ||
      gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED
    )
      if (
        balanceContext.balanceStatus.totalBalance >
        balanceContext.balanceStatus.betBalance
      ) {
        let newTokenList: any[] = [];
        tokenContext.tokenStatus.selectedToken.forEach((tokenList: any) => {
          tokenList.forEach((token: any) => {
            newTokenList.push(token);
          });
        });
        const newSelectedToken: Array<any> =
          tokenContext.tokenStatus.selectedToken;
        newSelectedToken.push(newTokenList);
        tokenContext.setTokenStatus((prev: any) => ({
          ...prev,
          selectedToken: newSelectedToken,
        }));

        balanceContext.setBalanceStatus((prev: any) => ({
          ...prev,
          totalBalance: prev.totalBalance - prev.betBalance,
          betBalance: prev.betBalance + prev.betBalance,
        }));
      }
  };
  return (
    <div className="flex flex-col justify-end items-end z-10 ">
      <div
        className={clsx(
          "transition-all duration-300 w-fit bg-black/70 border border-[#E6D183] rounded-l-[30px] flex flex-col justify-between py-3 ps-2 pe-1",
          gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED ||
            gameContext.gameStatus === socketEventsCode.SOCKET_PLACE_YOUR_BETS
            ? "h-full"
            : "h-[250px]"
        )}
      >
        <div className="flex flex-col gap-4 items-center">
          <button className="w-8" onClick={handleOpenStatisticsModal}>
            <PortraitStatistics className="w-full" />
          </button>
          <button className="w-8" onClick={handleOpenFavouriteModal}>
            <PortraitFavourite className="w-full" />
          </button>
        </div>
        {(gameContext.gameStatus === socketEventsCode.SOCKET_GAME_FINISHED ||
          gameContext.gameStatus ===
            socketEventsCode.SOCKET_PLACE_YOUR_BETS) && (
          <div className="flex flex-col gap-4">
            <div className="flex flex-col items-center">
              <p className="text-[8px] text-[#958963]">UNDO</p>
              <button className="w-8" onClick={handleUndo}>
                {<GameUndo className="w-full" />}
              </button>
            </div>
            <PortraitCoinSelect />
            <div className="flex flex-col items-center">
              <button className="w-8" onClick={handleDouble}>
                {<GameDouble className="w-full" />}
              </button>
              <p className="text-[8px] text-[#958963]">DOUBLE</p>
            </div>
          </div>
        )}
        <div className="flex flex-col gap-4 items-center">
          <button className="w-8" onClick={() => setIsMainBoard((prev: boolean) => !prev)}>
            <PortraitRaceTrackSwitch className="w-full" />
          </button>
          <button className="w-8">
            <PortraitAutoPlay className="w-full" />
          </button>
          <button className="w-8" onClick={handleOpenMenuModal}>
            <PortraitMenuIcon className="w-full" />
          </button>
        </div>
        <Statistics
          isShowModal={isStatisticsModalOpen}
          handleClose={handleCloseStatisticsModal}
        />
        <FavouiteModal
          isShowModal={isFavouriteOpen}
          handleClose={hanldeCloseFavouriteModal}
        />
        <MenuModal
          isShowModal={isMenuModalOpen}
          handleClose={handleCloseMenuModal}
        />
      </div>
      <img src={HottiesLogo} alt="hottieslogo" />
    </div>
  );
};
