import React from 'react'
import { ColGroupCell, ColorCell, EvenOddCell, HighLowCell, NormalCell, RowGroupCell, ZeroCell } from './maskData'
import OtherAreaMask from './OtherAreaMask'
import NumAreaMask from './NumAreaMask'
import ZeroAreaMask from './ZeroAreaMask'

const PortraitMainMask:React.FC = () => {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" className='absolute h-full top-0 left-0 overflow-visible' viewBox="0 0 305.421 733.384">
      <g id="Mask" transform="translate(-38.091 -34.148)">
        <g id="NumArea" >
          {NormalCell.map((cell: any, index: number) => (
            <g key={index}>
              {cell.tokenButtons.map((token:any) => (
                <NumAreaMask
                  key={token.id}
                  {...token}
                />
              ))}
            </g>
          ))}
          <ZeroAreaMask {...ZeroCell.tokenButtons[0]} />
        </g>
        <g id="LeftBoard" transform="translate(0 0)">
          {RowGroupCell.map((cell, index) => (
            <OtherAreaMask key={index} {...cell.tokenButtons[0]} />
          ))}
        </g>
        <g id="BottomArea">
          <g id="HalfArea" transform="translate(38.091 34.148)">
            <g id="BoardHalf">
              {HighLowCell.map((cell, index) => <OtherAreaMask {...cell.tokenButtons[0]} key={index} />)}
            </g>
            <g id="BoardEvenOdd">
              {EvenOddCell.map((cell, index) => <OtherAreaMask {...cell.tokenButtons[0]} key={index} />)}
            </g>
            <g id="BoardColor">
              {ColorCell.map((cell, index) => <OtherAreaMask {...cell.tokenButtons[0]} key={index} />)}
            </g>
          </g>
          <g id="ColArea">
            {ColGroupCell.map((cell, index) => (
              <OtherAreaMask key={index} {...cell.tokenButtons[0]} />
            ))}
          </g>
        </g>
      </g>
    </svg>
  )
}

export default PortraitMainMask